<template>
<div class="managerBottomCart animated" :class='bgFadeType' @click.self="close" >
	<div class="content animated" :class='contentFadeType'>
		<div class="header"><span>已选{{cart.list.length}}种美食 共{{cart.buyCount}}份</span> <button @click='clearCart'>清空</button></div>
		<ul class="list" v-if="cart.list.length>0">
			<li v-for="(prod, index) in cart.list" :key="index">
				<p class="name">{{prod.name}}</p>
				<p class="price">{{prod.price}}</p>
				<div class="set">
					<button :class='prod.buyCount>0 ? "on" : ""' class="less" @click="update(1,prod.id)"><i class="iconfont icon-shanchu6"></i></button>
					<span :class='prod.buyCount>0 ? "on" : ""'>{{prod.buyCount}}</span>
					<button class="add" @click="update(2,prod.id)"><i class="iconfont icon-jiahao2fill"></i></button>
				</div>
			</li>
		</ul>
	</div>
</div>

</template>

<script>
import { Toast, MessageBox } from 'mint-ui';	
export default {
	name: 'managerBottomCart',
	props:['shopID'],
	data () {
		return {
			show:false,
			bgFadeType:'',
			contentFadeType:'',
			shopId: this.$route.params.shopId||this.shopID,
			seller:{},
			cart:{//购物车商品列表
				buyCount:0, //统计当前店铺的购物数量
				total:0, //统计当前店铺的购物金额
				list:[]
			}
		}
	},
	created(){
		this.init();
	},
	mounted(){
		let oThis=this;
		this.BUS.$on('showCartList', ()=>{
			oThis.close();
		})
		this.BUS.$on('flush', ()=>{
			oThis.init();
		})	
		this.BUS.$on('clear', ()=>{
			if(this.show && this.contentFadeType=='fadeInUp'){
				this.bgFadeType='fadeOut';
				this.contentFadeType='fadeOutDown';
			}
		})	
	},	
	methods: {
		init(){
			if(localStorage[this.shopId]){
				this.seller = JSON.parse(localStorage[this.shopId]);
				//判断购物车信息
				if(this.seller.cart && this.seller.cart.buyCount>0){ console.log();
					this.cart=this.seller.cart;
				}else{
					this.cart={buyCount:0,total:0,list:[]};
				}
			}
		},
		update(t,prodId){
			if((t==1 || t==2) && prodId && this.shopId){
				this.BUS.$emit('getProdID',t,prodId,this.shopId);
				this.init();
			}else{
				if(!prodId) this.$toast({message: '未知商品ID', duration: 1200});
				if(!this.shopId) this.$toast({message: '未知店铺ID', duration: 1200});
			}
		},
		close(){
			if(this.show && this.contentFadeType=='fadeInUp'){
				this.bgFadeType='fadeOut';
				this.contentFadeType='fadeOutDown';
			}else{
				this.bgFadeType='fadeIn';
				this.contentFadeType='fadeInUp'; 
			}
			this.show=!this.show;
		},
		clearCart(){
			MessageBox.confirm('确定清空购物车吗？').then(action => {
				let goods=this.seller.goods;
				let list = this.cart.list;
				list.forEach(item => {
					goods.forEach(subItem => {
						if(item.id===subItem.id){
							subItem.buyCount=0;
						}	
					})
				})
				this.seller.goods = goods;
			  	this.cart={buyCount:0,total:0,list:[]};
			  	this.seller.cart={buyCount:0,total:0,list:[]};
			  	localStorage[this.shopId]=JSON.stringify(this.seller);
			  	Toast({message: '已清空', duration: 1200});
			  	setTimeout(()=>{this.BUS.$emit('clearCart')},200);
			  	setTimeout(()=>{
				  	this.bgFadeType='fadeOut';
					this.contentFadeType='fadeOutDown';
					this.show=!this.show;
				},500);
			}, action => {});
		}
	}
}
</script>

<style scoped lang="less">


.managerBottomCart{position: fixed;left: 0;top:0;bottom:50px;z-index: 0;width: 100%;height:calc(100% - 50px);opacity: 0;display:none;
	&.fadeIn{display:block;opacity:1;z-index:10;background-color: rgba(0,0,0,0.5);}
	&.fadeOut{display:none;opacity:0;z-index: 0;transition:all 0.5s;background-color: rgba(0,0,0,0);}
	.content{position: absolute;left: 0;right: 0;bottom:0px;max-height: 70%;height: auto;background-color: #fff;
		.header{width:100%;height: 50px;position: relative;border-bottom: 1px solid #eee;
			span{display:inline-block;line-height: 50px;color: #666;margin-left: 5%;}
			button{color:#2396ff;position: absolute;right: 5%;top:50%;z-index: 2;transform: translate(0,-50%);}
		}
		.list{
			li{padding:10px 5%;box-sizing: border-box;width:100%;display:flex;border-bottom: 1px dashed #eee;
				&:last-child{border-bottom:none;}
				.name{flex:6;color:#666;overflow: hidden;white-space: nowrap;text-overflow: ellipsis;padding-right: 10px;}
				.price{flex:1;color:#EC3C1F;
					&:first-child{content:"￥";font-size: 80%;}
				}
				.set{flex:1.5;overflow: hidden;display:flex;justify-content:flex-end;align-items: center; line-height: 20px;position: relative;justify-content: center;text-align: center;}
				button{padding:0;box-sizing: border-box;position: absolute;z-index: 2;}
				button i{color:#2396ff;}
				button,button i,button i:before{width:16px;height: 16px;display:inline-block;font-size: 16px;}
				span{flex:1;font-size:12px;justify-content: center;}
				.less{left: 0;}
				.add{right: 0;}
			}
		}
	}
}
</style>


