<template>
<ul id="shopGoodsList" ref="shopGoodsList">
	<li v-for="(item, index) in list" :key="index" :ref="item.enName" :class="item.enName">
		<div class="head">{{item.name}} <span>{{item.desc}}</span></div>
		<button v-for="(prod, subIndex) in item.products" :key="subIndex" class="food">
			<img :src="prod.icon" alt="" @click='showDetail(prod.id)' />
			<div class="con">
				<p class="name">{{prod.name}}</p>
				<p class="desc">{{prod.desc}}</p>
				<p class="info">
					<span>月售{{prod.sales}}</span>
					<span v-if="prod.applauseRate>0">好评{{prod.applauseRate}}%</span>
				</p>
				<div class="foot">
					<span class="price">{{prod.price}}</span>
					<div class="buy">
						<button :class='prod.buyCount>0 ? "on" : ""' class="less" @click="update(1,prod.id,$event)" :style='animate'><i class="iconfont icon-shanchu6"></i></button>
						<span :class='prod.buyCount>0 ? "on" : ""'>{{prod.buyCount}}</span>
						<button class="add" @click="update(2,prod.id,$event)"><i class="iconfont icon-jiahao2fill"></i></button>
					</div>
				</div>
			</div>
		</button>
	</li>
</ul>
</template>

<script>
import BScroll from 'better-scroll';
export default {
	name: 'shopGoods',
	data () {
		return {
			shopId: this.$route.params.shopId,
			foods:{},
			goods:{},
			list:[],
			animate:{}
		}
	},
	created(){
		this.init();
		setTimeout(()=>{
			this.animate={"animation":"lessRotate .5s linear"};
		},500)
	},
	watch: {
		list(){
			return this.list;
		}
	},
	mounted() {
		this.BUS.$on('clearCart', ()=>{
			this.init();
		})
		this.BUS.$on('flush', ()=>{
			this.init();
		})
	},
	methods: {
		init(){
			if(localStorage[this.shopId]){
				let shop=JSON.parse(localStorage[this.shopId]);
				this.foods=shop.foods;
				this.goods=shop.goods;
			}
			//先得到大类 及 所属ids
			var arr=this.getCategoryName(this.foods);
			//遍历大类里面ids，通过ids获取具体商品信息，组装在大类products里
			this.getGoodsList(arr);
		},
		//遍历大类里面ids，通过ids获取具体商品信息，组装在大类products里
		getGoodsList (arr) {
			arr.forEach(item=>{
				item.products=[];
				item.list.forEach(subItem=>{
					this.goods.forEach(third=>{
						subItem===third.id ? item.products.push(third) : "";
					})
				})
			})
			this.list=arr;
		},
		//获取商品左侧类目名称时，从 “热销” 开始
		getCategoryName (list) {
			let arr=[];
			list.forEach((item,index)=>{
				index>1 ? arr.push(item) : ""
			})
			return arr;
		},
		//往购物车减少/增加商品数量
		update(t,prodId,e){
			this.BUS.$emit('getProdID',t,prodId,e,this.shopId);
			this.init();
		},
		showDetail(prodId){ 
			localStorage.shopId=this.shopId;
			this.$router.push(`/shop/food/${prodId}`);
		}
	}
}
</script>

<style scoped lang="less">
ul#shopGoodsList{width:100%;height: auto;
	li{margin-bottom: 20px;
		.head{background-color: #f5f5f5;padding:10px 0 10px 20px;box-sizing:border-box;
			span{color: #999;font-size: .8em;}
		}
		.food{width:100%;display:flex;padding:10px 0 10px 10px;box-sizing:border-box;
			img{width:25%;display:inline-block;}
			.con{width: 75%;display:inline-block;padding-left: 10px;box-sizing:border-box;
				p{margin-bottom: 3px;}
				.name,.desc,.info{text-align: left;overflow: hidden;white-space: nowrap;text-overflow: ellipsis;}
				.desc,.info{color:#999;font-size: .8em;}
				.foot{width:100%;overflow:hidden;}
				.price{float:left;color: #ff5339;font-size:18px;margin-top:-2px;
					&:before{content:"￥";font-size: 14px;}
				}
				.buy{min-width:90px;margin-right:5%;float: right;text-align: right;position: relative;font-size: 0px;justify-content: flex-end;
					button{padding:0;box-sizing: border-box;}
					button i{color:#2396ff;}
					button,button i,button i:before{width:16px;height: 16px;display:inline-block;font-size: 16px;}
					span{opacity: 0;font-size:12px;position: relative;top:-2px;min-width: 24px;display:inline-block;text-align: center;transition: all 0s;}
					span.on{opacity: 1;transition: all 2s;}
					.add{position: relative;z-index: 3;}
					.less{opacity: 0;position: absolute;top:0;right: 0;z-index: 2;transition: all .5s;}
					@keyframes lessRotate{
						from {transform:rotate(540deg);transform-origin:center center;opacity: 1;right: 40px;}
						to {transform:rotate(0deg);transform-origin:center center;opacity: 0;right: 0;}
					}
					@-moz-keyframes lessRotate{
						from {transform:rotate(540deg);transform-origin:center center;opacity: 1;right: 40px;}
						to {transform:rotate(0deg);transform-origin:center center;opacity: 0;right: 0;}
					}
					@-webkit-keyframes lessRotate{
						from {transform:rotate(540deg);transform-origin:center center;opacity: 1;right: 40px;}
						to {transform:rotate(0deg);transform-origin:center center;opacity: 0;right: 0;}
					}
					@-o-keyframes lessRotate{
						from {transform:rotate(540deg);transform-origin:center center;opacity: 1;right: 40px;}
						to {transform:rotate(0deg);transform-origin:center center;opacity: 0;right: 0;}
					}
					.less.on{animation:lessRotate2 .5s linear;right: 40px;opacity: 1;}
					@keyframes lessRotate2{
						from {transform:rotate(0deg);transform-origin:center center;opacity: 0;right: 0;}
						to {transform:rotate(360deg);transform-origin:center center;opacity: 1;right: 40px;}
					}
					@-moz-keyframes lessRotate2{
						from {transform:rotate(0deg);transform-origin:center center;opacity: 0;right: 0;}
						to {transform:rotate(360deg);transform-origin:center center;opacity: 1;right: 40px;}
					}
					@-webkit-keyframes lessRotate2{
						from {transform:rotate(0deg);transform-origin:center center;opacity: 0;right: 0;}
						to {transform:rotate(360deg);transform-origin:center center;opacity: 1;right: 40px;}
					}
					@-o-keyframes lessRotate2{
						from {transform:rotate(0deg);transform-origin:center center;opacity: 0;right: 0;}
						to {transform:rotate(360deg);transform-origin:center center;opacity: 1;right: 40px;}
					}
				}
			}
		}
	}
}

</style>


