<template>
<div class="shopComment">
	<div class="header w oh">
		<div class="fl">
			<span class="score">4.2</span>
			<span class="tips">综合评分</span>
			<span class="gt">高于周边商家69.2%</span>
		</div>
		<div class="fr">
			<dl>
				<dt>服务态度</dt>
				<dd>
					<span class="star">
						<i class="iconfont icon-biaoxingfill on"></i>
						<i class="iconfont icon-biaoxingfill on"></i>
						<i class="iconfont icon-biaoxingfill on"></i>
						<i class="iconfont icon-biaoxingfill on"></i>
						<i class="iconfont icon-biaoxingfill"></i>
					</span>
					<span class="score">4.1</span>
				</dd>
			</dl>
			<dl>
				<dt>商品评分</dt>
				<dd>
					<span class="star">
						<i class="iconfont icon-biaoxingfill on"></i>
						<i class="iconfont icon-biaoxingfill on"></i>
						<i class="iconfont icon-biaoxingfill on"></i>
						<i class="iconfont icon-biaoxingfill on"></i>
						<i class="iconfont icon-biaoxingfill"></i>
					</span>
					<span class="score">4.3</span>
				</dd>
			</dl>
			<dl>
				<dt>送达时间</dt>
				<dd>38分钟</dd>
			</dl>
		</div>
	</div>
	<div class="switch">
		<button :class="switchClass[0]" @click="switchComment(0)">全部 {{comment.list.length}}</button>
		<button :class="switchClass[1]" @click="switchComment(1)">好评 {{good.length}}</button>
		<button :class="switchClass[2]" @click="switchComment(2)">有图 {{havePicture.length}}</button>
		<button :class="switchClass[3]" @click="switchComment(3)">吐槽 {{bad.length}}</button>
	</div>
	<div class="haveText">
		<button @click='doHave'><i class="iconfont icon-xuanzhong" :class='have'></i> 只看有内容的评价</button>
	</div>
	<div class="content">
		<ul class="list">
			<li v-for="(l,index) in list" :key="index">
				<div class="avatar"><img :src="updateAvatar(l.avatar)" alt=""></div>
				<div class="detail">
					<div class="user">
						<span class="name fl">{{l.username}}</span><span class="date fr">{{l.rated_at}}</span>
					</div>
					<div class="star">
						<span v-if="l.rating_star>0">
							<i class="iconfont icon-biaoxingfill on" v-for="a in getArray(l.rating_star)"></i>
						</span><span v-if="l.rating_star<5">
							<i class="iconfont icon-biaoxingfill" v-for="b in getArray(5-l.rating_star)"></i>
						</span>
						<span v-if="l.time_spent_desc" class="time_desc">{{l.time_spent_desc}}</span>
					</div>
					<div class="text">{{l.rating_text}}</div>
					<div class="food" v-if='l.item_rating_list.length>0'>
						<span v-for="(r, index) in l.item_rating_list" :key="index">
							<i class="iconfont" :class="r.rating_star>3 ? 'icon-zantongfill' : 'icon-meiyoudingdan-01'"></i> {{r.rate_name}}
						</span>
					</div>
					<div class="haveImage" v-if='l.item_rating_list.length>0'>
						<img v-for='(pic, index) in getPicture(l.item_rating_list)' :src="pic" alt="" v-preview="pic" preview-title-enable="true" preview-nav-enable="true" />
					</div>
				</div>
			</li>
		</ul>
	</div>
</div>
</template>

<script>
import imagePreview from 'vue-image-preview';
import BScroll from 'better-scroll';
export default {
	name: 'shopComment',
	data () {
		return {
			avatar:'http://shadow.elemecdn.com/faas/desktop/media/img/default-avatar.38e40d.png',
			shopId: this.$route.params.shopId,
			comment:{},
			list:[],
			good:[],
			bad:[],
			havePicture:[],
			haveText:[],
			have:'',
			switchClass:[],
			commentScroll:''
		}
	},
	created(){
		this.init();
	},
	mounted(){
		// this.$nextTick(()=>{
		// 	this.commentScroll = new BScroll('.content',{});
		// })
	},
	methods: {
		//初始化
		init(){
			this.switchClass[0]='on';
			this.comment.list=require('../../assets/db/comments.json');
			this.list=this.comment.list;
			let list=this.list;
			this.good=this.getGood();
			this.bad=this.getBad();
			this.havePicture=this.getHavePicture();
		},
		//组装头像
		updateAvatar(v){ return v ? this.stringBuilder(v) : this.avatar },
		//返回一个指定长度的数组 智能显示星星图案 
		getArray(n){ let a=[]; a.length=n; return a },
		//切换查看有内容和所有
		doHave(){
			let i=0;
			this.switchClass.forEach((item,index)=>{item=='on'? i=index : ''});
			if(this.have){//查看全部
				this.have=''; this.showAll(i);
			}else{//查看有内容的评价
				this.have='on'; this.showHaveText(i);
			}
		},
		//查看全部
		showAll(i){
			let arr=[];
			switch(i){
				case 0: arr=this.comment.list; break;
				case 1: arr=this.getGood(); break;
				case 2: arr=this.getHavePicture(); break;
				case 3: arr=this.getBad(); break;
			}
			this.list=arr;
		},
		//只看有内容的评价
		showHaveText(i){
			let arr=[];
			switch(i){
				case 0: arr=this.filterText(this.comment.list); break;
				case 1: arr=this.filterText(this.getGood()); break;
				case 2: arr=this.filterText(this.getHavePicture()); break;
				case 3: arr=this.filterText(this.getBad()); break;
			}
			this.list=arr;
		},
		filterText(list){
			return list.filter(item=>item.rating_text!='');
		},
		//获取好评的评价 >=3颗星
		getGood(){ return this.comment.list.filter(item=>item.rating_star>=3) },
		//获取吐槽的评价 <3颗星
		getBad(){ return this.comment.list.filter(item=>item.rating_star<3) },
		//获取有图片的评价
		getHavePicture(){
			let arr=[];
			this.comment.list.forEach(item=>{
				if(item.item_rating_list.length>0){
					let child=item.item_rating_list;
					child.forEach(sub=>{
						sub.image_hash ? arr.push(item) : ''
					})
				}
			});
			return arr;
		},
		//全部  好评  有图  吐槽  之间切换
		switchComment(t){
			this.switchClass=[];
			let arr=[];
			switch(t){
				case 0: 
					this.have ? arr=this.filterText(this.comment.list) : arr=this.comment.list;
					this.switchClass[0]='on'; break;
				case 1: 
					this.have ? arr=this.filterText(this.getGood()) : arr=this.getGood(); 
					this.switchClass[1]='on'; break;
				case 2: 
					this.have ? arr=this.filterText(this.getHavePicture()) : arr=this.getHavePicture(); 
					this.switchClass[2]='on'; break;
				case 3: 
					this.have ? arr=this.filterText(this.getBad()) : arr=this.getBad(); 
					this.switchClass[3]='on'; break;
			}
			this.list=arr;
		},
		//组装评价里面的图片
		getPicture(list){
			let arr=[];
			list.forEach(item=>{
				if(item.image_hash){
					arr.push(this.stringBuilder(item.image_hash));
				}
			})
			return arr;
		},
		//字符串拼接
		stringBuilder(s){
			let a=s.substr(0,1);
			let b=s.substr(1,2);
			let c=s.substr(3);
			return `http://fuss10.elemecdn.com/${a}/${b}/${c}.jpeg`;
		}
	}
}
</script>

<style scoped lang="less">
.shopComment{
	.header{padding: 30px 5% 20px;box-sizing:border-box;border-bottom: 10px solid #eee;margin-bottom: 20px;border-top: 1px solid #eee;
		.fl{ border-right: 1px solid #eee; box-sizing: border-box;width: 40%;text-align: center;
			span{display:block;text-align: center;}
			.score{color: #f90;font-size: 2em;margin-bottom: 10px;}
			.tips{color: #666;font-size: .9em;}
			.gt{color: #999;font-size: .8em;}
		}
		.fr{width:60%;padding-left: 5%;box-sizing:border-box;position: relative;top:-2px;
			dl{line-height: 25px;width:100%;overflow:hidden;
				&:last-child{dd{color:#999;}}
				dt{float: left;color:#666;font-size: 13px;}
				dd{color:#eee;padding-left: 15px;box-sizing: border-box;float:left;
					.star{
						.on{color:#f90;}
					}
					.score{color:#f90;}
				}
			}
		}
	}
	.switch{width: 100%;display:flex;padding:0 5%;box-sizing:border-box;margin-bottom: 10px;
		button{width: 18%;height: 30px;line-height: 30px;border-radius: 2px;margin-right: 3%;color: #666; padding: 0;-webkit-transition: all 0.3s;transition: all 0.3s; background-color: #dfebf7;font-size: 14px;}
		.on{background-color: #2396ff;color: #fff;}
	}
	.haveText{line-height: 50px;padding:0 5%;box-sizing:border-box;
		button{color:#666;
			i{color:#999;}
			.on{color:#00b43c;}
		}
	}
	.content{/*max-height: 600px;overflow-y: auto;*/
		.list{border-top: 1px solid #eee;padding-top: 10px;margin-bottom: 100px;
			li{display: flex;padding:10px;box-sizing:border-box;border-bottom: 1px dashed #eee;
				&:last-child{border-bottom:none;}
				.avatar{width:40px;
					img{width:40px;height: 40px;border-radius: 100%;}
				}
				.detail{width:calc(100% - 40px);padding-left: 10px;box-sizing:border-box;
					.user{width:100%;overflow:hidden;font-size: 13px;
						.name{color:#666;}
						.date{color:#999;}	
					}
					.star{
						span{
							i{color:#ddd;font-size: 12px;}
							i.on{color:#f90;}
						}
						.time_desc{font-size: 12px;color: #999;padding-left: 10px;position: relative;top:1px;}
					}
					.text{color:#333;font-size: 14px;margin-bottom: 5px;}
					.food{
						i{font-size: 12px;}
						i.icon-zantongfill{color:#00a0dc;}
						i.icon-meiyoudingdan-01{color:#000;}
						span{color:#999;font-size: 12px;border:1px solid #ddd;padding:0px 5px;margin-right: 5px;border-radius: 1px;display:inline-block;margin-bottom: 5px;}
					}
					.haveImage{width:100%;height: auto;padding:10px 0 0 0;box-sizing: border-box;overflow: hidden;
						img{width:23%;height: auto;display:block;float:left;margin-right: 2.333%;margin-bottom:2.333%;border-radius: 5px;}
						img:nth-child(4n){margin-right: 0;}
					}
				}
			}
		}
	}
}
</style>


