<template>
	<div class="seller" ref="sellerHome" v-if="shop">
		<shopHeader v-if="shop && shop.shop" ref="shopHead"></shopHeader>

		<div class="mainContent">
			<header class="tc shopHeaderTab" :class="shopHeaderFixed" ref="shopHeaderTab">
				<button :class="switchShopHeader[0]" @click="switchShopHeaderHandler(0)">点餐</button>
				<button :class="switchShopHeader[1]" @click="switchShopHeaderHandler(1)">评价<span class="count">({{this.shop.comment.list.length}})</span></button>
				<button :class="switchShopHeader[2]" @click="switchShopHeaderHandler(2)">商家<span class="story">有故事</span></button>
			</header>
			<swiper :options="swiperOption" class="shopContent" ref="swiperOption" :style="'height:'+shopContentHeight+'px;'">
			    <swiper-slide ref="shopfoods">
			    	<shopfoods v-if="shop && shop.foods"></shopfoods>
			    </swiper-slide>
			    <swiper-slide ref="shopComment">
					<shopComment v-if="shop && shop.comment && shop.comment.list"></shopComment>
			    </swiper-slide>
			    <swiper-slide ref="shopInfo">
					<shopInfo v-if="shop && shop.shop && shop.shop.id"></shopInfo>
			    </swiper-slide>
			</swiper>
		</div>
		<shopBottomCart v-show="showShopBottomCart" v-if="shop && shop.foods"></shopBottomCart>
		<managerBottomCart></managerBottomCart>
	</div>
</template>

<script>
import shopHeader from './header.vue';
import shopfoods from './foods.vue';
import shopComment from './comment.vue';
import shopInfo from './info.vue';
import shopBottomCart from './cart.vue';
import managerBottomCart from './managerBottomCart.vue';
import axios from 'axios';
import { InfiniteScroll, Indicator } from 'mint-ui';
export default {
	name: 'seller',
	components: {shopHeader,shopfoods,shopComment,shopInfo,shopBottomCart,managerBottomCart},
	data () {
		var _this=this;
		return {
			shopId: this.$route.params.shopId,
			shop:{},
			switchShopHeader:['on','',''], //给 点餐 评价 商家 大类标题增加高亮样式
			shopHeaderFixed:'', //给 点餐 评价 商家 大类标题增加固定在顶部的样式
			showShopBottomCart:true, //只有在点餐类目下才显示底部购物车
			shopContentHeight:'auto',
			swiperOption:{ //点餐 评价 商家 大类的滑动
				on: {
			    	slideChangeTransitionEnd: function(){
			      		_this.switchShopHeaderLight(this.activeIndex);
			      		_this.setShopContentHeight(this.activeIndex);
			      		window.scroll(0,0);
			    	}
			  	}
			}
		}
	},
	created(){
		window.scroll(0,0);
		this.getShopByShopId(); 
	},
	mounted(){ 
		var oThis=this;
		this.$nextTick(() => {
			setTimeout(()=>{
				this.shopContentHeight=this.$refs.shopfoods.$el.offsetHeight;
				// 点餐 评价 商家 大类标题高度
				var shopHeaderTabHeight=this.$refs.shopHeaderTab.clientHeight; 
				//商家头部高度
				var shopHeaderHeight=50;
				if(this.$refs.shopHead){
					shopHeaderHeight=this.$refs.shopHead.$el.clientHeight || this.$refs.shopHead.clientHeight;
				}
				window.onscroll = e => {
					var e =e || window.event;
					var scrolltop=document.documentElement.scrollTop||document.body.scrollTop; //滚动高度
		  			if(this.$route.name==="sellerHome"){
		  				if(scrolltop>=shopHeaderHeight){ 
		  					//如果页面向上滚动高度大于商家头部高度，则点餐 评价 商家菜单 固定在顶部 浮动
		  					this.shopHeaderFixed="fixed"; 
		  					if(this.$refs.sellerHome && this.$refs.sellerHome.style){
		  						this.$refs.sellerHome.style.paddingTop="50px"
		  					}
		  				}else{
		  					this.shopHeaderFixed="";
		  					this.$refs.sellerHome ? this.$refs.sellerHome.style.paddingTop="0px" : '';
		  				}
		  			}
				};
			})
		})
		this.BUS.$on('showInstruction', (flag)=>{
			if(flag){
				document.querySelector(".mainContent").className='mainContent animated fadeOutDown';
				document.querySelector(".seller").style.height=document.querySelector(".shopHeader").offsetHeight+'px';
			}else{
				document.querySelector(".mainContent").className='mainContent animated fadeInUp';
				document.querySelector(".seller").style.height='auto';
			}
		})
	},
	methods: {
		//获取店铺信息
		getShopByShopId () {
			/*Indicator.open({text: '加载中...', spinnerType: 'fading-circle'});
			axios.get(`/api/seller?shopId=${this.shopId}&t=${Math.random().toString().substr(2)}`)
			     .then(res=>{
			     	Indicator.close();
			     	if(JSON.stringify(res.data.data)==="{}"){
			     		this.$toast("未查到店铺信息");
			     	}else{
			     		this.shop=res.data.data;
			     	}
			     })
			     .catch(err=>{
			     	Indicator.close();
			     	console.log(`获取店铺信息失败: ${err}`);
			     })*/
			var sellerList=require('../../assets/db/sellerList.json');
			sellerList.forEach(item => {
				item.shop.id === this.shopId ? this.shop=item : ''
			})
			this.shop.comment.list=require('../../assets/db/comments.json');
			if(!localStorage[this.shopId]){
				localStorage[this.shopId]=JSON.stringify(this.shop);
			}
		},
		//切换 点餐  评价 商家
		switchShopHeaderHandler (i) {
			this.switchShopHeaderLight(i);
			this.$refs.swiperOption.swiper.slideTo(i, 300, false);
			this.setShopContentHeight(i);
      		window.scroll(0,0);
		},
		//高亮 点餐  评价 商家
		switchShopHeaderLight (i) {
			this.switchShopHeader=[];
			this.switchShopHeader[i]='on';
			i===0 ? this.showShopBottomCart=true : this.showShopBottomCart=false;
		},
		setShopContentHeight(i){
			switch(i){
      			case 0 : this.shopContentHeight=document.querySelector(".shopFoods").offsetHeight||0; break;
      			case 1 : this.shopContentHeight=document.querySelector(".shopComment").offsetHeight||0; break;
      			case 2 : this.shopContentHeight=document.querySelector(".shopInfo").offsetHeight||0; break;
      		}
		}
	}
}
</script>

<style scoped lang="less">

.seller{background-color:#fff;position: relative;padding-top:0;overflow:hidden;
	.shopHeaderTab{width:100%;height:50px;max-width:640px;overflow:hidden;background-color: #fff;
		&.fixed{position: fixed;top:0;left: 50%;transform: translate(-50%,0);z-index: 10;border-bottom: 1px solid #eee;box-sizing:border-box;}
		button{width:33.33%;float: left;position: relative;height:50px;line-height: 50px;text-align: center;
			span{position: absolute;top:2px;font-size: .8em;}
			.count{color:#999;padding-left: 5px;}
			.story{background-color:#DFF2FF;color:#2395ff;padding:1px 4px 2px;border-radius: 5px;margin-left: 5px;line-height:normal;position: relative;top:0px;}
			&.on{color: #2395ff;
				span{color: #2395ff;}
			}
		}
	}
}
</style>


