<template>
<div class="profile">
	<router-link to="/profile/information" class="header">
		<img :src="meicon" alt="" />
		<span class="name">vlonsir</span>
		<span class="phone"><i class="iconfont icon-shouji3"></i>15901155011</span>
		<span class="icon iconfont icon-more"></span>
	</router-link>
	<div class="hot">
		<router-link to="/profile/benefit">
			<span class="num red">0</span>
			<span class="text">红包</span>
		</router-link>
		<router-link to="/profile/ecoin">
			<span class="num green">20</span>
			<span class="text">金币</span>
		</router-link>
	</div>

	<router-link to="/profile/myAddress" class="a mb10"><i class="iconfont icon-dizhiguanli"></i>我的地址<i class="iconfont icon-more"></i></router-link>
	
	<router-link to="/coinMall/index" class="a bm1"><i class="iconfont icon-taojinbi"></i>金币商城<i class="iconfont icon-more"></i></router-link>
	<router-link to="/profile/share" class="a mb10"><i class="iconfont icon-gifts"></i>分享拿10元现金<i class="iconfont icon-more"></i></router-link>

	<router-link to="/profile/service" class="a bm1"><i class="iconfont icon-service"></i>我的客服<i class="iconfont icon-more"></i></router-link>
	<router-link to="/profile/download" class="a bm1"><i class="iconfont icon-dibu"></i>下载饿了么APP<i class="iconfont icon-more"></i></router-link>
	<router-link to="/profile/rules" class="a mb10"><i class="iconfont icon-survey"></i>规则中心<i class="iconfont icon-more"></i></router-link>

	<router-link to="/profile/privacyPolicy" class="privacyPolicy">隐私政策</router-link>

</div>
</template>

<script>
import meicon from '@/assets/images/role.jpeg'
export default {
	name: 'profile',
	components: {},
	data () {
		return {
			msg: 'profile',
			meicon
		}
	},
	watch: {
	
	},
	created(){
		document.title="个人中心";
	},
	computed: {
	
	},
	methods: {
	
	}
}
</script>

<style scoped lang="less">
.profile{width:100%;height: 100%;background-color: #f5f5f5;
	a{background-color: #fff;}
	.header{width:100%;min-height:60px;height:auto;padding:50px 0 0 0;background-image: linear-gradient(90deg,#0af,#0085ff);display: block;position: relative;
		img{position: absolute;top:20px;left: 20px;z-index: 2; width:60px;height: 60px;border-radius: 100%;display:block;}
		.name{position: absolute;top:30px;left:100px;z-index: 2;font-size: 1.5em;color: #fff;font-weight: bolder;}
		.phone{position: absolute;top:60px;left:100px;z-index: 2;font-size: .9em;color: #fff;line-height: 12px;
			i{position: relative;top:1px;}
		}
		.icon{position: absolute;top:50px;right:20px;z-index: 2;font-size: 1.2em;color: #fff;}
	}
	.hot{width:100%;display:flex;margin-bottom: 10px;
		a{width:50%;text-align: center;color:#333;padding:20px 0;
			&:first-child{border-right:1px solid #eee;}
			span{display:block;font-size: 1.8em;font-weight:bolder;}
			.text{font-size: .9em;color:#666;}
			.red{color:#ff5f3e;
				&:after{content:"个";font-weight: normal;font-size: .5em;}
			}
			.green{color:#6ac20b;&:after{content:"个";font-weight: normal;font-size: .5em;}}
		}
	}
	.a{display:block;height:40px;line-height: 40px;padding:0 15px;box-sizing: border-box;position: relative;
		.icon-more{position: absolute;top:3px;right: 10px;}
		i:first-child{font-size: 1.3em;font-weight: bolder;margin-right: 10px;}
		.icon-dizhiguanli{color:#4AA5F0;position: relative;top:2px;}
		.icon-taojinbi{color:#94D94A;position: relative;top:2px;}
		.icon-gifts{color:#FC7B53;position: relative;top:2px;}
		.icon-service{color:#4AA5F0;}
		.icon-dibu{color:#4AA5F0;}
		.icon-survey{color:#4AA5F0;}
	}
	.bm1{border-bottom: 1px solid #eee;}
	.privacyPolicy{display:block;height:50px;line-height: 50px;text-align: center;background-color: transparent;color: #29B1FF;}
}
</style>


