<template>
<div class="shopTopic">
	<swiper :options="swiperShopHeaderBannerOption" class="swiperShopHeaderBannerOption">
		<swiper-slide v-for="(item,index) in topic.list" :key="index">	
			<img :src="item.icon" alt="" />
		</swiper-slide>
	</swiper>	
</div>
</template>

<script>
export default {
	name: 'shopTopic',
	data () {
		return {
			shopId: this.$route.params.shopId,
			topic:{},
			swiperShopHeaderBannerOption:{} //点餐 专题滑动
		}
	},
	created(){
		if(localStorage[this.shopId]){
			let seller=JSON.parse(localStorage[this.shopId]);
			if(seller.foods && seller.foods[0] && seller.foods[0].list && seller.foods[0].list.length>0){
				this.topic=seller.foods[0];
			}
		}
	}
}
</script>
<style scoped lang="less">
.shopTopic{width:100%;
	.swiperShopHeaderBannerOption{width:100%;
		.swiper-slide{padding:0 10px;box-sizing:border-box;}
		img{width:100%;display:block;height: auto;}
	}
}
</style>


