import Vue from 'vue'
import Router from 'vue-router'
import Index from '@/components/Index'
import find from '@/components/find/Index'
import findIndex from '@/components/find/home'

import order from '@/components/order/index'
import orderList from '@/components/order/list'
import orderDetail from '@/components/order/detail'

import me from '@/components/me/index'
import profile from '@/components/me/profile'
import information from '@/components/me/information'
import myAddress from '@/components/me/address'
import benefit from '@/components/me/benefit'
import ecoin from '@/components/me/ecoin'
import share from '@/components/me/share'
import service from '@/components/me/service'
import download from '@/components/me/download'
import rules from '@/components/me/rules'
import privacyPolicy from '@/components/me/privacyPolicy'

import coinMall from '@/components/coinMall/index'
import coinMallHome from '@/components/coinMall/home'

import seller from '@/components/seller/index'
import sellerHome from '@/components/seller/home'
import food from '@/components/seller/food'

import search from '@/components/search/index'
import searchHome from '@/components/search/home'
import searchResult from '@/components/search/result'


Vue.use(Router)

export default new Router({
  routes: [{
      path: '/',
      name: 'Index',
      component: Index
    },{
      path: '/find',
      name: 'find',
      component: find,
      children:[{
        path:'index',
        name:'findIndex',
        component: findIndex
      }]
    },{
      path: '/order',
      name: 'order',
      component: order,
      children:[{
        path:'index',
        name:'orderList',
        component: orderList
      },{
        path:'detail/:orderID',
        name:'orderDetail',
        component: orderDetail
      }]
    },{
      path: '/profile',
      name: 'me',
      component: me,
      children:[{
          path:'index',
          name:'profile',
          component: profile
        },{
          path:'information',
          name:'information',
          component: information
        },{
          path:'myAddress',
          name:'myAddress',
          component: myAddress
        },{
          path:'benefit',
          name:'benefit',
          component: benefit
        },{
          path:'ecoin',
          name:'ecoin',
          component: ecoin
        },{
          path:'share',
          name:'share',
          component: share
        },{
          path:'service',
          name:'service',
          component: service
        },{
          path:'download',
          name:'download',
          component: download
        },{
          path:'rules',
          name:'rules',
          component: rules
        },{
          path:'privacyPolicy',
          name:'privacyPolicy',
          component: privacyPolicy
        }
      ] 
    },{
      path: '/coinMall',
      name: 'coinMall',
      component: coinMall,
      children:[{
          path:'',
          name:'coinMallHome',
          component: coinMallHome
        },{
          path:'index',
          name:'coinMallHome',
          component: coinMallHome
        }
      ]
    },{
      path: '/shop',
      name: 'seller',
      component: seller,
      children:[{
          path:':shopId',
          name:'sellerHome',
          component: sellerHome
        },{
          path:'index/:shopId',
          name:'sellerHome',
          component: sellerHome
        },{
          path:'food/:foodID',
          name:'food',
          component: food
        }
      ]
    },{
      path: '/search',
      name: 'search',
      component: search,
      children:[{
          path:'',
          name:'searchHome',
          component: searchHome
        },{
          path:'result',
          name:'searchResult',
          component: searchResult
        }
      ]
    }


  ]
})



