<template>
	<div class="shopHeader" v-if="shop">
		<header class="header pr" ref='header'>
			<img class="top" ref='headerBg' src="https://fuss10.elemecdn.com/c/e0/283549d417057f6829e829af1a2eapng.png" alt="">
			<button class="back" @click="$router.go(-1)"><i class="iconfont icon-fanhui1"></i></button>
			<!-- <button class="search"><i class="iconfont icon-sousuoleimu"></i></button>
			<button class="share"><i class="iconfont icon-skip1"></i></button> -->
			<span class="logo" ref="logo"><img v-if="shop.logo" :src="shop.logo" alt="" /><i v-if="shop.tag">{{shop.tag}}</i></span>
			<button class="collect" ref="collect" @click="doCollect"><i class="iconfont icon-xihuanfill" :class="collect_icon"></i></button>
		</header>
		
		<section class="baseInfo pt50">
			<p class="name tc f18 mb10">{{shop.name}}</p>
			<p class="tc">
				<span v-if="shop.score">评价{{shop.score}}</span>
				<span v-if="shop.sales" class="ml10 mr10">月售{{shop.sales}}</span>
				<span v-if="shop.hummingbird">{{shop.hummingbird}}约{{shop.duration}}分钟</span>
			</p>
			<button class="adoptive" @click="switchInstruction">
				<div class="w oh huodong">
					<div class="fl" v-if="getHuoDong(shop.offer).list && getHuoDong(shop.offer).list.length>0">
						<i class="youh">{{getHuoDong(shop.offer).name}}</i>
						<span v-for="(sol,index) in getHuoDong(shop.offer).list" :key="index">{{sol.subName}}</span>
					</div>
					<div class="fr">{{getYouHuiCount(shop.offer)}}个优惠 <i class="iconfont" :class="iconXiang"></i></div>
				</div>
				<div class="gonggao" v-if="shop.announcement">公告：{{shop.announcement}}</div>
			</button>
		</section>

		<section class="part3">
			<div class="instruction">
				<dl>
					<dt>优惠</dt>
					<dd class="youhui">
						<p v-for="(item,index) in shop.offer" v-if="item.list.length>0" :key="index">
							<i :class="youhuoStyle(item.name)">{{item.name}}</i>
							<span v-for="(subItem,index) in item.list" :key="index">{{subItem.name}}</span>
						</p>
					</dd>
				</dl>
				<dl v-if="shop.service.length>0">
					<dt>服务</dt>
					<dd class="fuwu">
						<p v-for="(s,index) in shop.service" :key="index">
							<i>{{s.name}}</i>
							<span>{{s.desc}}</span>
						</p>
					</dd>
				</dl>
				<dl v-if="shop.announcement">
					<dt>公告</dt>
					<dd class="gonggao">{{shop.announcement}}</dd>
				</dl>
			</div>
		</section>

	</div>
</template>

<script>
export default {
	name: 'shopHeader',
	data () {
		return {
			shopId: this.$route.params.shopId,
			shop:{},
			showInstruction:false,
			collect_icon:'on',
			iconXiang:'icon-xiangxia2'
		}
	},
	created(){
		if(localStorage[this.shopId]){
			let shop=JSON.parse(localStorage[this.shopId]);
			this.shop=shop.shop;
		}
	},
	mounted(){
		var _this=this;
		this.$nextTick(()=>{ 
			let shopHeaderHeight = _this.$refs.headerBg.height;
			window.addEventListener("scroll", function(){
				var sc=document.documentElement.scrollTop || document.body.scrollTop || window.scrollY;
				if(sc<100){
					_this.$refs.header ? _this.$refs.header.style.height=shopHeaderHeight-window.scrollY+'px' : '';
					_this.$refs.header ? _this.$refs.header.style.opacity=(100-window.scrollY)/100 : '';
					_this.$refs.logo ? _this.$refs.logo.style.transform='translate(-50%,-5%) scale('+(100-window.scrollY)/100+')' : '';
					_this.$refs.logo ? _this.$refs.logo.style.top=62-window.scrollY/10+"%" : '';
					_this.$refs.collect ? _this.$refs.collect.style.top=91-window.scrollY/5+"%" : '';
				}
			});
		})
	},
	methods: {
		//分别显示优惠样式
		youhuoStyle(i){
			let s="";
			switch(i){
				case "满减" :
				case "折扣" :
				case "特价" :
				case "红包" : s="youh"; break;
				case "新客" :
				case "首单" : s="shoudan"; break;
				case "会员" : s="huiyuan"; break;
			}
			return s;
		},
		//显示在公告上面的活动
		getHuoDong (list) {
			let obj={};
			for(let i=0;i<list.length;i++){
				if(list[i].list.length>0){
					obj=list[i];
					break;
				}
			}
			return obj;
		},
		// 切换显示优惠明细
		switchInstruction () {
			this.showInstruction = !this.showInstruction;
				if(this.showInstruction){
					document.querySelector(".instruction").style.display="block";
					document.querySelector(".instruction").className="instruction animated fadeIn";
					this.iconXiang='icon-xiangshang2';
				}else{
					document.querySelector(".instruction").className="instruction animated fadeOut";
					setTimeout(()=>{document.querySelector(".instruction").style.display="none"},200);
					this.iconXiang='icon-xiangxia2';
				}
			
			this.BUS.$emit('showInstruction', this.showInstruction);
		},
		//返回优惠数量
		getYouHuiCount (objList) {
			let n=0;
			objList.forEach(item=>{
				item.list.length>0 ? n++ : "";
			})
			return n;
		},
		doCollect(){
			if(this.collect_icon){
				this.collect_icon='';
				this.$toast({message:"已取消收藏", duration:1200});
			}else{
				this.collect_icon='on';
				this.$toast({message:"收藏成功", duration:1200});
			}
		}
	}
}
</script>

<style scoped lang="less">
.fade-enter-active, .fade-leave-active {transition: opacity .5s;}
.fade-enter, .fade-leave-active {opacity:0;}
.shopHeader{position: relative;
	.header{opacity: 1;
		button{position:absolute;top:10px;z-index:2;color:#fff;
			&:hover{cursor:pointer;}
		}
		.iconfont{font-size:1.5em;}
		.back .iconfont{left: 10px;font-size: 1.5em;}
		.share{right: 10px;}
		.search{right: 50px;}
		img.top{display: block;width: 100%;height: auto;}
		.collect{position:absolute;top:91%;right: 20px;background-color: #fff;width: 30px;height: 30px;text-align: center;line-height: 30px;display:block;border-radius: 100%;padding:0;box-shadow: 0px 3px 3px #ddd;
			i{position: relative;top:3px;font-size:1.5em;color:#999;transition: all 0.3s;}
			i.on{color:#f55050;}
		}
		.logo{width: 100px;display:block;position:absolute;left: 50%;top:62%;z-index: 5; border-radius: 3px;transform:translate(-50%,-5%);overflow:hidden;
			img{display:block;width: 100%;height: auto;border-radius: 3px;}
			i{font-style: normal;background-color: yellow;position: absolute;top:0;left: 0;z-index: 2;padding:0px 3px;border-radius: 3px 0 3px 0;}
		}
	}
	.adoptive{width:100%;display:block;padding:15px 10px 5px;box-sizing: border-box;
		.huodong{height:30px;
			.fl{color: #666;
				i{font-style: normal;border-width:1px;border-style:solid;padding:1px 5px;border-radius: 3px;box-sizing: border-box;}
				.youh{color: #ce7b77;background-color: #fff4f2;border-color: #f4d3d1;}
				.shoudan{color: #768663;background-color: #eaf9e7;border-color: #d1dfc5;}
				.huiyuan{color: #b66d30;background-color: #fffef7;border-color: #e3d3b0;}
				span{margin-left: 5px}
			}
			.fr{font-size: .8em;color: #999;
				i{font-size: .8em;}
			}
		}
		.gonggao{overflow: hidden;white-space: nowrap;text-overflow: ellipsis;color: #999;}	
	}
	.part3{width:100%;overflow:hidden;}
	.instruction{padding: 20px 10px;box-sizing: border-box;margin-bottom:20px;display:none;
		i{font-style: normal;}
		dl{margin-bottom: 10px;}
		dt{font-weight: bolder;height: 40px;line-height: 40px;color: #333;position: relative;
			.shouqi{position: absolute;right: 0;top: 0;z-index: 2;font-size:12px;color:#999;
				i{position: relative;top:2px;}
			}
		}
		dd{font-size: .8em;color: #666;}
		.youhui{
			p{line-height: 20px;width:100%;margin-bottom: 10px;}
			i{float:left;border-width:1px;border-style:solid;padding:0 5px;height:21px;border-radius: 3px;box-sizing: border-box;margin-right: 5px;}
			.youh{color: #ce7b77;background-color: #fff4f2;border-color: #f4d3d1;}
			.shoudan{color: #768663;background-color: #eaf9e7;border-color: #d1dfc5;}
			.huiyuan{color: #b66d30;background-color: #fffef7;border-color: #e3d3b0;}
		}
		.fuwu{
			p{line-height: 20px;width:100%;margin-bottom: 10px;}
			i{float:left;border:1px solid #ddd;padding:0 5px;height:21px;border-radius: 3px;box-sizing: border-box;margin-right: 5px;}	
			span{word-wrap:break-word;word-break:break-all;line-height: 20px;display:table;}
		}
	}
}
</style>


