<template>
<div class="home">
	<div class="header pr">
		<router-link to="/coinMall/index" class="left">
			<span class="tit">金币商城</span>
			<span class="desc">0元好物在这里</span>
			<img :src="png1" alt="">
		</router-link>
		<router-link to="/profile/share" class="rightTop">
			<span class="tit">推荐有奖</span>
			<span class="desc">10元现金拿不停</span>
			<img :src="jpeg2" alt="">
		</router-link>
		<router-link to="" class="rightBottom">
			<span class="tit">周边优惠</span>
			<span class="desc">领取口碑好券</span>
			<img :src="png3" alt="">
		</router-link>
	</div>
	<router-link to="/coinMall/index" class="more">查看更多</router-link>
</div>
</template>

<script>
import png1 from '@/assets/images/1.png'
import jpeg2 from '@/assets/images/2.jpeg'
import png3 from '@/assets/images/3.png'
export default {
	name: 'home',
	components: {},
	data () {
		return {
			msg: 'home',
			png1,
			jpeg2,
			png3
		}
	},
	watch: {
	
	},
	created(){
		document.title="发现"; 
	},
	computed: {
	
	},
	methods: {
	
	}
}
</script>

<style scoped lang="less">
.home{
	.header{width:100%;overflow:hidden;margin-bottom: 10px;
		a{display: inline-block;background-color: #fff;float: left;
			span{display:block;}
			.tit{font-size: 1.2em;position: relative;top:20px;left:20px;}
			.desc{color:#999;font-size: .8em;position: relative;top:20px;left:20px;}
		}
		.left{position: absolute;top:0;left: 0; width:50%;border-right: 1px solid #eee;box-sizing: border-box;height: 160px;
			img{position: absolute;right: 10px;bottom: 10px;z-index: 2; width: 35%;display:block;height: auto;}
			.tit{color:#ff9900;}
		}
		.rightTop,.rightBottom{margin-left: 50%;width:50%;height: 80px;position: relative;}
		.rightTop,.rightBottom{img{position:absolute;right: 10px;top: 50%;z-index: 2;display:block;height: auto;transform: translate(0,-50%);}}
		.rightTop{border-bottom: 1px solid #eee;box-sizing: border-box;
			img{width: 25%;}
			.tit{color:#1ba9e1;}
		}
		.rightBottom{
			img{width: 27%;}
			.tit{color:#ed6660;}
		}
	}
	.more{display:block;background-color: #fff;height: 50px;line-height: 50px;text-align: center;width: 100%;color:#999;}

}
</style>


