<template>
<div class="shopRecommend" id="shopRecommend" v-if="recommend && recommend.list && recommend.list.length>0">
	<div class="shopRecommendTitle">商家推荐</div>	
	<swiper :options="swiperShopTuiJianOption" class="swiperShopTuiJianOption">
		<swiper-slide v-for="(r,index) in products" :key="index">
			<button class="item">
				<img :src="r.icon" alt="" @click="showDetail(r.id)" />
				<p class="name">{{r.name}}</p>
				<p class="info">
					<span>月售{{r.sales}}</span>
					<span class="haoping" v-if="r.applauseRate>0">好评{{r.applauseRate}}%</span>
				</p>
				<div class="foot">
					<span class="price">{{r.price}}</span>
					<span class="mark"></span>
					<div class="buy" v-if="r.stock>0">
						<button class="less" :class='r.buyCount>0 ? "on" : ""' @click="update(1,r.id,$event)" :id="'less'+index" :style='animate'>
							<i class="iconfont icon-shanchu6"></i>
						</button>
						<span :class='r.buyCount>0 ? "on" : ""'>{{r.buyCount}}</span>
						<button class="add" @click="update(2,r.id,$event)"><i class="iconfont icon-jiahao2fill"></i></button>
					</div>
					<div class="buy" v-else>
						已售完
					</div>
				</div>
			</button>
		</swiper-slide>
	</swiper>
</div>
</template>

<script>
export default {
	name: 'shopRecommend',
	data () {
		return {
			shopId: this.$route.params.shopId,
			goods:{},
			recommend:{},
			products:[],
			swiperShopTuiJianOption:{ //商家推荐的滑动特效
				slidesPerView: 3,
      			spaceBetween: 5,
			},
			animate:{}
		}
	},
	created() {
		this.init();
		setTimeout(()=>{
			this.animate={"animation":"lessRotate .5s linear"};
		},500)
	},
	mounted(){
		this.BUS.$on('clearCart', ()=>{
			this.init();
		})
		this.BUS.$on('flush', ()=>{
			this.init();
		})
	},
	watch: {
		products(){
			return this.products;
		}
	},
	methods: {
		init(){
			if(localStorage[this.shopId]){ 
				let seller=JSON.parse(localStorage[this.shopId]);
				this.goods=JSON.parse(JSON.stringify(seller.goods)); 
				if(seller.foods && seller.foods[1] && seller.foods[1].list && seller.foods[1].list.length>0){
					this.recommend=JSON.parse(JSON.stringify(seller.foods[1]));
					this.products=this.getGoodsList(this.recommend.list);
					console.log();
				}else{
					this.recommend={};
				}
			}
		},
		//返回商品列表 商家推荐
		getGoodsList (ids) {
			var prods=[];
			var list=this.goods;
			ids.forEach(id=>{
				list.forEach(item=>{
					if(id===item.id){
						prods.push(item);
					}
				})
			})
			return prods;
		},
		//往购物车减少/增加商品数量
		update(t,prodId,e){
			this.BUS.$emit('getProdID',t,prodId,e,this.shopId);
			this.init();
		},
		showDetail(prodId){
			localStorage.shopId=this.shopId;
			this.$router.push(`/shop/food/${prodId}`);
		}
	}
}
</script>

<style scoped lang="less">
.shopRecommend{padding-bottom: 20px;
	.shopRecommendTitle{margin:0 10px;padding:20px 0 5px;}
	.swiperShopTuiJianOption{margin:0 10px;overflow:hidden;
		.name{overflow: hidden;white-space: nowrap;text-overflow: ellipsis;text-align: left;}
		.info{font-size: 10px;color:#999;text-align: left;
			span:first-child{margin-right:5px;}
		}
		.swiper-slide{box-shadow:0px 0px 2px #eee;box-sizing: border-box;}
		button.item{width:100%;display:block;
			p{margin-bottom: 5px;}
			img{width:100%;display:block;height: auto;}
			.foot{width:100%;display:flex;
				.price{width:50px;color: #ff5339;
					&:before{content:"￥";font-size: .8em;}
				}
				.buy{min-width:90px;float: right;text-align: right;position: relative;justify-content: flex-end;
					button{padding:0;box-sizing: border-box;}
					button i{color:#2396ff;}
					button,button i,button i:before{width:16px;height: 16px;display:inline-block;font-size: 16px;}
					span{opacity: 0;font-size:12px;position: relative;top:-2px;min-width: 24px;display:inline-block;text-align: center;transition: all 0s;}
					span.on{opacity: 1;transition: all 2s;}
					.add{position: relative;z-index: 3;}
					.less{opacity: 0;position: absolute;top:0;right: 0;z-index: 2;transition: all .5s;}
					@keyframes lessRotate{
						from {transform:rotate(540deg);transform-origin:center center;opacity: 1;right: 40px;}
						to {transform:rotate(0deg);transform-origin:center center;opacity: 0;right: 0;}
					}
					@-moz-keyframes lessRotate{
						from {transform:rotate(540deg);transform-origin:center center;opacity: 1;right: 40px;}
						to {transform:rotate(0deg);transform-origin:center center;opacity: 0;right: 0;}
					}
					@-webkit-keyframes lessRotate{
						from {transform:rotate(540deg);transform-origin:center center;opacity: 1;right: 40px;}
						to {transform:rotate(0deg);transform-origin:center center;opacity: 0;right: 0;}
					}
					@-o-keyframes lessRotate{
						from {transform:rotate(540deg);transform-origin:center center;opacity: 1;right: 40px;}
						to {transform:rotate(0deg);transform-origin:center center;opacity: 0;right: 0;}
					}
					.less.on{animation:lessRotate2 .5s linear;right: 40px;opacity: 1;}
					@keyframes lessRotate2{
						from {transform:rotate(0deg);transform-origin:center center;opacity: 0;right: 0;}
						to {transform:rotate(360deg);transform-origin:center center;opacity: 1;right: 40px;}
					}
					@-moz-keyframes lessRotate2{
						from {transform:rotate(0deg);transform-origin:center center;opacity: 0;right: 0;}
						to {transform:rotate(360deg);transform-origin:center center;opacity: 1;right: 40px;}
					}
					@-webkit-keyframes lessRotate2{
						from {transform:rotate(0deg);transform-origin:center center;opacity: 0;right: 0;}
						to {transform:rotate(360deg);transform-origin:center center;opacity: 1;right: 40px;}
					}
					@-o-keyframes lessRotate2{
						from {transform:rotate(0deg);transform-origin:center center;opacity: 0;right: 0;}
						to {transform:rotate(360deg);transform-origin:center center;opacity: 1;right: 40px;}
					}
				}
			}
		}
	}
}
@media screen and (max-width:320px){
	.shopRecommend .swiperShopTuiJianOption .info span.haoping{display:none;}
	.shopRecommend .swiperShopTuiJianOption button.item .foot .buy{min-width:60px;}
	.shopRecommend .swiperShopTuiJianOption button.item .foot .buy button{padding:0;}
}
@media screen and (max-width:375px){
	.shopRecommend .swiperShopTuiJianOption .info span.haoping{display:none;}
	.shopRecommend .swiperShopTuiJianOption button.item .foot .buy{min-width:65px;}
	.shopRecommend .swiperShopTuiJianOption button.item .foot .buy button{padding:0;}
}
</style>


