<template>
<div class="list">
	<ul>
		<li v-for="(l,index) in list" :key="index">
			<router-link :to="'/order/detail/'+l.id"></router-link>
			<img :src="l.shopLogo" alt="" />
			<div class="right">
				<div class="head">
					<div class="fl">{{l.shopName}}</div>
					<div class="fr">{{l.status}}</div>
				</div>
				<div class="middle">
					<div class="fl">{{l.createTime}}</div>
					<div class="fr">{{l.statusNote}}</div>
				</div>
			</div>
			<div class="bottom">
				<div class="fl">
					<span class="prod" v-html="getDetail(l.products)"></span>
					<span class="total">{{l.products.length}}</span>
				</div>
				<div class="fr">{{l.totalMoney | formatPrice}}</div>
			</div>
		</li>
	</ul>
</div>
</template>

<script>
import axios from 'axios';
import { InfiniteScroll, Popup, Indicator } from 'mint-ui';
export default {
	name: 'list',
	data () {
		return {
			list: []
		}
	},
	filters:{
		formatPrice(value){
			return parseFloat(value).toFixed(2)
		}
	},
	created(){
		document.title="订单"; 
		this.getOrderList();
	},
	methods: {
		getOrderList () {
			/*Indicator.open({text: '加载中...', spinnerType: 'fading-circle'});
			axios.get("/api/orders?t="+Math.random().toString().substring(2))
			     .then(res=>{
			     	this.list=res.data.data;
			     	Indicator.close();
			     })
			     .catch(err=>{
			     	console.log("获取订单列表失败: ", err);
			     	this.$toast("获取订单列表失败");
			     	Indicator.close();
			     })*/
			this.list=require('../../assets/db/orderList.json');
		},
		getDetail(arr){
			let str="";
			arr.forEach((a)=>{
				str+="+"+a.name
			})
			str=str.substring(1);
			return str;
		}
	}
}
</script>

<style scoped lang="less">
.list{
	ul{padding-bottom: 50px;
		li{position: relative;padding:20px 10px;width:100%;box-sizing: border-box;border-bottom: 1px dashed #eee;background-color: #fff;
			&:last-child{border-bottom:none;}
			a{position: absolute;left: 0;top:0;z-index: 2;width:100%;height: 100%;display: block;}
			img{position: absolute;left: 10px;top:20px;z-index: 1; width:50px;height:50px;display: inline-block;}
			.right{margin-left: 60px;width:calc(100% - 60px);height:50px;border-bottom:1px solid #eee;box-sizing: border-box;
				.head,.middle{width: 100%;overflow:hidden;}
				.head,.bottom{
					.fl{overflow: hidden;white-space: nowrap;text-overflow: ellipsis;}
				}
				.head{height:25px;line-height:25px;
					.fl{font-size: 1.2em;width: 80%;color:#333;}
					.fr{color:#999;font-size:.9em;}
				}
				.middle{color:#999;height:20px;line-height:20px;font-size:.85em;
					div{overflow: hidden;white-space: nowrap;text-overflow: ellipsis;}
					.fr{width: 62%;text-align:right;}
				}
				
			}
			.bottom{padding-left:60px;padding-top:5px;height:30px;line-height:30px;display:block;box-sizing: border-box;
				.fl{width: 80%;font-size:.8em;
					span{display: inline-block;overflow: hidden;white-space: nowrap;text-overflow: ellipsis;}
					.prod{max-width:60%;color:#666;}
					.total{padding-left:5px;max-width:40%;color:#999;
						&:before{content:"等";}
						&:after{content:"件商品";}
					}
				}
				.fr{color:#E71A2A;
					&:before{content:"￥";font-size:10px;}
				}
			}
			
		}
	}
}
</style>


