<template>
<div class="shopFoods">
	<shopTopic v-if="foods && foods[0] && foods[0].list" :shopId="shopId"></shopTopic>
	<shopRecommend v-if="foods && foods[1] && foods[1].list" :shopId="shopId"></shopRecommend>
	<div class="foods" :class="foodsFixed" :style="'height:'+shopHeight+'px;'">
		<div class="left foodsLeft" ref="foodsLeft">
			<shopCategory v-if="foods && foods.length>2" :shopId="shopId"></shopCategory>
		</div>
		<div class="right foodsRight" :style="'height:'+shopHeight+'px;'">
			<div class="foodsRightTop">{{foodsRightTop.name}} <span>{{foodsRightTop.desc}}</span></div>
			<div class="shopGoods" ref="shopGoods" id="shopGoods" :style="'height:'+(shopHeight-200)+'px;'">
				<shopGoods v-if="goods" :shopId="shopId"></shopGoods>
			</div>
		</div>
	</div>
</div>
</template>

<script>
import shopTopic from './topic.vue';
import shopRecommend from './recommend.vue';
import shopCategory from './category.vue';
import shopGoods from './goods.vue';
import BScroll from 'better-scroll';

export default {
	name: 'shopFoods',
	components: {shopTopic,shopRecommend,shopCategory,shopGoods},
	data () {
		return {
			shopId: this.$route.params.shopId,
			foodsFixed:'',
			foods:{},
			goods:{},
			foodsRightTop:{name:'',desc:''},
			shopHeight:0,
			rightScrollHeight:[],
			rightBscroll:''
		}
	},
	created(){
		this.init();
	},
	mounted () {
		this.$nextTick(() => {
			setTimeout(()=>{
				let sc=document.getElementById("shopRecommend").clientHeight;
				let sr=document.querySelector(".shopCategory").offsetHeight < 500 ? 500 : document.querySelector(".shopCategory").offsetHeight;
				this.shopHeight=sc+sr-60;
			})
			this.computeLiHeight();
			this._initBScroll();
		})

		this.BUS.$on('flush', ()=>{
			this.init();
		})
		this.BUS.$on('switchCategory', index=>{
			setTimeout(()=>{
				this.foodsRightTop=this.foods[index+2];
			},800)
			let rsh=this.rightScrollHeight;
			this.rightBscroll.scrollTo(0, -rsh[index], 700);
		})
	},
	methods: {
		init(){
			if(localStorage[this.shopId]){
				let shop=JSON.parse(localStorage[this.shopId]);
				this.foods=shop.foods;
				this.goods=shop.goods;
				this.foodsRightTop=this.foods[2];
			}else{
				this.$toast('店铺ID错误');
			}
		},
		computeLiHeight(){
			let foods=this.foods;
			let arr=[];
			for(let i=2;i<foods.length;i++){
				let a=0;
				for(let j=2;j<=i;j++){
					a+=document.querySelector('.'+foods[j].enName).offsetHeight+20
				}
				arr.push(a);
			}
			this.rightScrollHeight=arr;
			this.rightScrollHeight.unshift(0);
		},
		_initBScroll(){
			let _this=this;
			this.rightBscroll = new BScroll('.shopGoods',{
				probeType:2,   //在滚动中触发scroll 事件
				click: true,
				taps: true,
				pullDownRefresh:{
					threshold: 50, // 当下拉到超过顶部 50px 时，触发 pullingDown 事件
  					stop:20 // 刷新数据的过程中，回弹停留在距离顶部还有 20px 的位置  然后一个laoding提示正在请求数据
				},
				pullUpLoad: {
					threshold: 50
				}
			});
			let rsh=this.rightScrollHeight;
			this.rightBscroll.on('scroll', pos=>{
				for(let i=1; i<rsh.length; i++){
					if(Math.abs(pos.y)<rsh[i]){ 
						_this.BUS.$emit('leftCategory', i-1);
						this.foodsRightTop=this.foods[i+1];
						return false;
					}
				}
			})
			//下拉动作
		 	this.rightBscroll.on('pullingDown', pos => {
		 		//document.querySelector(".foodsRightTop").style.display='none';
	            this.$toast({message:"加载中...", duration:1000});
	            setTimeout(()=>{
	            	//document.querySelector(".foodsRightTop").style.display='block';
		            this.rightBscroll.finishPullDown();
		            this.BUS.$emit('showPopup',true,'top','加载完毕',2000);
		        },1500)
          	})
          	//上拉动作
		 	this.rightBscroll.on('pullingUp', pos => {
	            this.$toast({message:"加载更多", duration:1200});
	            setTimeout(()=>{
	            	this.rightBscroll.finishPullUp();
	            	this.BUS.$emit('showPopup',true,'top','加载完毕',2000);
		        },1500)
          	})
		}
	}
}
</script>

<style scoped lang="less">
.shopFoods{
	.foods{padding:0px 0px 29px 0px;display:flex;box-sizing:border-box;overflow:hidden;
		&.fixed{position: fixed;left: 50%;top:0;z-index: 10;width:100%;max-width: 640px;transform: translate(-50%,0);
			.left{height:100%;}
			.right{height:100%;overflow-y:auto;}
		}
		.left{width:100px;background-color: #F8F8F8;min-height:500px;}
		.right{position: relative; width:calc(100% - 100px);height:auto;background-color: #fff;box-sizing:border-box;
			.foodsRightTop{position: absolute;left: 0;top: 0;z-index: 2;background-color: #f5f5f5;padding: 10px 0 10px 20px;box-sizing:border-box;width:100%;
				span{color: #999;font-size: 0.8em;}
			}
			.shopGoods{width:100%;height: auto;position: relative;z-index: 1;}
		}
	}
}
</style>


