<template>
<div class="shopCategory" id="shopCategory">
	<button v-for="(category,index) in arr" :key="index" :class="categoryNameStyle(index)" @click="switchThisCategory(index)">
		<i></i>
		<span>{{category.name}}</span>
		<em></em>
	</button>
</div>
</template>

<script>
export default {
	name: 'shopCategory',
	data () {
		return {
			shopId: this.$route.params.shopId,
			foods:{},
			arr:[],
			currentCategoryIndex:0
		}
	},
	created(){
		if(localStorage[this.shopId]){
			let shop=JSON.parse(localStorage[this.shopId]);
			this.foods=shop.foods;
			this.arr=this.getCategoryName(this.foods);
		}
	},
	mounted(){
		this.BUS.$on('leftCategory',index=>{
			setTimeout(()=>{
				this.currentCategoryIndex=index;
			},500)
		})
	},
	methods: {
		//获取商品左侧类目名称时，从 “热销” 开始
		getCategoryName (list) {
			let arr=[];
			list.forEach((item,index)=>{
				index>1 ? arr.push(item) : ""
			})
			return arr;
		},
		//点击 切换为当前类目商品
		switchThisCategory (i) {
			this.currentCategoryIndex=i;
			this.BUS.$emit('switchCategory',i);
		},
		//返回当前按钮的高亮状态
		categoryNameStyle (i) {
			return this.currentCategoryIndex===i ? "on" : ""
		}
	}
}
</script>

<style scoped lang="less">
.shopCategory{width:100px;background-color: #F8F8F8;
	button{display:block;width: 100%;height: 50px;line-height: 50px;position: relative;transition:all 0.3s;
		span{color: #666;display:block;text-align: left;padding-left: 10%;}
		&.on{background-color: #fff;}
	}
}
</style>


