<template>
  <div class="index" ref="index">
  	<section ref="topSection" class="block">
		<router-link to="/profile/myAddress" class="address" ref="myAddress">
			<i class="iconfont icon-zuobiaofill"></i>
			<span>{{locationPosition}}</span>
			<i class="iconfont icon-xiangxia1"></i>
		</router-link>
	    <div class="header" :class="headFixed" ref="searchHeader">
			<router-link to="/search" class="search">
				<i class="iconfont icon-search"></i>
				<span>搜索饿了么商家、商家名称</span>
			</router-link>
	    </div>

		<swiper :options="swiperMenuOption" class="hotMenu">
		    <swiper-slide v-for="(hm,index) in hotMenu" :key="index">
				<router-link :to="hm.url"><img :src="hm.icon" alt=""/>{{hm.name}}</router-link>
		    </swiper-slide>
		    <div class="swiper-pagination" slot="pagination"></div>
		</swiper>

		<section class="channelList">
			<router-link v-for="(cl,index) in channelList" :key="index" :to="cl.url">
				<img :src="cl.icon" alt=""/>{{cl.name}}</router-link>
		</section>

		<router-link class="openMember" to="">
			<img :src="wangguan" class="wangguan" alt=""/>
			<span class="bolder">超级会员 ·</span>
			<span class="desc"> 每月领20元红包</span>
			<i class="iconfont icon-arrowright"></i>
			<span class="tips">立即开通</span>
		</router-link>
		
		<router-link class="qualityPackage" to="">
			<img :src="taocan" alt="" />
			<span class="tit">品质套餐</span>
			<span class="subTit">搭配齐全吃得好</span>
			<span class="buy">立即抢购 &gt;</span>
		</router-link>
		
		<swiper :options="swiperAdBannerOption" class="adBanner">
		    <swiper-slide v-for="(ad,index) in adBanner" :key="index">
				<router-link :to="ad.url"><img :src="ad.icon" alt=""></router-link>
		    </swiper-slide>
		</swiper>
		
		<p class="sellerTop">—— 推荐商家 ——</p>
	</section>

	<div class="sellerHeader" :class="sellerHeaderFixed" ref="sellerHeader">
		<button>综合排序<i class="iconfont icon-xiangxia1"></i></button>
		<button>距离最近</button>
		<button>品质联盟</button>
		<button>筛选<i class="iconfont icon-androidgengduo"></i></button>
	</div>
	<ul class="sellerList" v-if="sellers.length>0">
		<li class="w oh" v-for="(item,index) in sellers" :key="index">
			<button class="cover" @click.stop="goShop(item.shop.id)">
				<div class="fl">
					<img :src="item.shop.logo" alt="" />
					<span class="tag" v-if="item.shop.tag" :class="tagStyle(item.shop.tag)">{{item.shop.tag}}</span>
				</div>
				<div class="fr">
					<p class="sellerName w oh">
						<span class="fl">{{item.shop.name}}</span>
						<i class="fr iconfont icon-androidgengduo"></i>
					</p>
					<p class="w oh">
						<span class="score fl"><i class="iconfont icon-biaoxingfill"></i>{{item.shop.score}}</span>
						<span class="sales fl">{{item.shop.sales}}</span>
						<span v-if="item.shop.hummingbird" class="fr hummingbird">{{item.shop.hummingbird}}</span>
						<span v-if="item.shop.onTime" class="fr onTime">{{item.shop.onTime}}</span>
					</p>
					<p class="w oh fontGray">
						<span class="fl">起送￥{{item.shop.startUpSend||0}}</span>
						<span v-if="item.shop.freight>0" class="fl">配送￥{{item.shop.freight}}</span>
						<span v-else class="fl">配送免费</span>
						<span class="fr">{{item.shop.distance}}km</span>
						<span class="fr">{{item.shop.duration}}分钟</span>
					</p>
					<p v-if="item.shop.evaluate" class="evaluate">“{{item.shop.evaluate}}”</p>
					<div class="preferentialService" :id="item.shop.id">
						<span class="favourable" 
							v-for="(so,index) in item.shop.offer" 
							:key="index" 
							v-if="so.list.length>0" 
							:class="index<6 ? 'youhui' : index===6 ? 'huiyuan' : ''">
							<i v-for="(sof,index) in so.list" :key="index">{{sof.subName}}</i>
						</span>
						<span class="service" v-if="item.shop.service.length>0">
							<i v-for="(sos,index) in item.shop.service" :key="index">{{sos.name}}</i>
						</span>
						<button class="preferentialServiceMore" @click.stop="preferentialServiceMore(item.shop.id)"><i class="iconfont icon-jiantouxia"></i></button>
					</div>					
					<p class="nearby" v-if="item.shop.nearby.length>0">附近还有{{item.shop.nearby.length}}家{{item.shop.subName}}
						<button class="nearbyMore" @click.stop="nearbyMore(index)"><i class="iconfont icon-jiantouxia"></i></button>
					</p>
				</div>
			</button>
		</li>
	</ul>

	
	<mt-popup v-model="popupVisible" position="middle" v-if="sellers.length>0"> 
		<ul>
			<li v-for="(nearby,index) in sellers[popupIndex].shop.nearby" :key="index" class="w oh">
				<span class="fl ml10">{{nearby.name}}</span>
				<span class="fr mr10">{{nearby.distance}}km</span>
				<span class="fr mr10">{{nearby.duration}}分钟</span>
			</li>
		</ul>
	</mt-popup>


  </div>
</template>

<script>
import wangguan from '@/assets/images/wangguan.png'
import meishi from '@/assets/images/meishi.png'
import wancan from '@/assets/images/wancan.png'
import chaoshi from '@/assets/images/chaoshi.png'
import shuiguo from '@/assets/images/shuiguo.png'
import yiyao from '@/assets/images/yiyao.png'
import xianhua from '@/assets/images/xianhua.png'
import hanbao from '@/assets/images/hanbao.png'
import shucai from '@/assets/images/shucai.png'
import zhaji from '@/assets/images/zhaji.png'
import difangcai from '@/assets/images/difangcai.png'
import malatang from '@/assets/images/malatang.png'
import kuaican from '@/assets/images/kuaican.png'
import xiaochi from '@/assets/images/xiaochi.png'
import dapai from '@/assets/images/dapai.png'
import mianshi from '@/assets/images/mianshi.png'
import baozi from '@/assets/images/baozi.png'
import taocan from '@/assets/images/taocan.png'
import adBanner1 from '@/assets/images/adBanner1.jpg'
import adBanner2 from '@/assets/images/adBanner2.jpg'
import adBanner3 from '@/assets/images/adBanner3.jpg'
import adBanner4 from '@/assets/images/adBanner4.jpg'
import koubei from '@/assets/images/koubei.jpg'
import taopiaopiao from '@/assets/images/taopiaopiao.jpg'
import feizhu from '@/assets/images/feizhu.jpg'
import huoche from '@/assets/images/huoche.jpg'
import { InfiniteScroll, Popup, Indicator } from 'mint-ui';
import axios from 'axios';
export default {
  name: 'index',
  data () {
    return {
      wangguan, taocan,koubei,taopiaopiao,feizhu,huoche,
      swiperMenuOption: {
      	slidesPerView: 5, //一组显示5个
  		slidesPerGroup: 5, //一组显示5个
      	slidesPerView: 5, //一行显示3个
    	slidesPerColumn: 2, //显示2行
    	slidesPerColumnFill: 'row', //行布局
    	pagination: {el: '.swiper-pagination'}
      },
      swiperAdBannerOption: {autoplay: true},
      hotMenu:[
      	{name:"美食", icon:meishi, url:""},
      	{name:"晚餐", icon:wancan, url:""},
      	{name:"商超便利", icon:chaoshi, url:""},
      	{name:"水果", icon:shuiguo, url:""},
      	{name:"医疗健康", icon:yiyao, url:""},
      	{name:"浪漫鲜花", icon:xianhua, url:""},
      	{name:"汉堡披萨", icon:hanbao, url:""},
      	{name:"厨房生鲜", icon:shucai, url:""},
      	{name:"炸鸡炸串", icon:zhaji, url:""},
      	{name:"地方菜系", icon:difangcai, url:""},
      	{name:"麻辣烫", icon:malatang, url:""},
      	{name:"素食简餐", icon:kuaican, url:""},
      	{name:"地方小吃", icon:xiaochi, url:""},
      	{name:"大牌惠吃", icon:dapai, url:""},
      	{name:"米粉面馆", icon:mianshi, url:""},
      	{name:"包子粥店", icon:baozi, url:""}
      ],
      adBanner:[
      	{icon:adBanner1, url:""},
      	{icon:adBanner2, url:""},
      	{icon:adBanner3, url:""},
      	{icon:adBanner4, url:""}
      ],
      channelList:[
      	{name:"口碑", icon:koubei, url:""},
      	{name:"电影", icon:taopiaopiao, url:""},
      	{name:"酒店", icon:feizhu, url:""},
      	{name:"火车票", icon:huoche, url:""}
      ],
      sellers:[],
      sellersData:require("../assets/db/sellerList.json"), //[]
      popupVisible:false,
      headFixed:"",
      sellerHeaderFixed:"",
      popupIndex:0,
      addressHeight:0, //搜索框上面  地址的高度
      topSectionHeight:0, //整个筛选标题以上的所有内容的高度总和
      searchHeaderHeight:0, //头部搜索框的高度
      sellerHeaderHeight:0, //综合排序 筛选标题的高度
      isBottom:false, //是否滚动到了底部，且数据已经加载完
      locationPosition:"正在定位..."
    }
  },
  created () {
  	document.title="点外卖";
  	this.getSellers();
  	//this.getLocationPosition();
  },
  mounted () { 
	this.addressHeight=this.$refs.myAddress.$el.clientHeight;
  	this.topSectionHeight=this.$refs.topSection.clientHeight; 
  	this.searchHeaderHeight=this.$refs.searchHeader.clientHeight;
  	this.sellerHeaderHeight=this.$refs.sellerHeader.clientHeight;
  	window.onscroll = e => {
  		if(this.$route.name==="Index"){
			var e =e || window.event;
			var scrolltop=document.documentElement.scrollTop||document.body.scrollTop; //滚动高度
				if(scrolltop>=this.addressHeight && scrolltop<(this.topSectionHeight-this.searchHeaderHeight)){
					this.$refs.index.style.paddingTop=this.searchHeaderHeight+"px";
					this.sellerHeaderFixed="";
					this.headFixed="fixed";
				}else if(scrolltop>=(this.topSectionHeight-this.searchHeaderHeight)){
					this.$refs.index.style.paddingTop=(this.searchHeaderHeight+this.sellerHeaderHeight)+"px";
					this.sellerHeaderFixed="fixed";
					this.headFixed="fixed";
				}else if(scrolltop<this.addressHeight){
					this.$refs.index.style.paddingTop="0px";
					this.sellerHeaderFixed="";
					this.headFixed="";
				}
			var scrollHeight=document.body.scrollHeight; //内容高度
			var clientHeight=document.documentElement.clientHeight || document.body.clientHeight || document.body.offsetHeight; //视图窗口高度 
			//判断如果滚动到了底部，则执行加载更多
			if((scrolltop+clientHeight) === scrollHeight){
				if(!this.isBottom)this.loadMore();
			}
		}
	}	
  },
  methods:{
  	getSellers () {
  		/*axios.get("/api/restapi/shopping/v3/restaurants?latitude=39.7719&longitude=116.500834&offset=0&limit=8&extras[]=activities&extras[]=tags&extra_filters=home&rank_id=&terminal=h5&t="+Math.random().toString().substring(2))
  		     .then(res => {
  		     	console.log(res);
  		     })
  		     .catch(err => {
  		     	console.log(err);
  		     });*/


  		/*Indicator.open({text: '加载中...', spinnerType: 'fading-circle'});
  		axios.get("/api/sellers?t="+Math.random().toString().substring(2))
  		     .then(res=>{
  		     	this.sellersData=res.data.data;
  		     	this.sellers.push(this.sellersData[0]);
			  	this.sellers.push(this.sellersData[1]);
			  	this.sellers.push(this.sellersData[2]);
			  	Indicator.close();
  		     })
  		     .catch(err=>{
  		     	console.log(err);
  		     	this.$toast("查询店铺列表失败");
  		     	Indicator.close();
  		     });*/
  		this.sellers.push(this.sellersData[0]);
	  	this.sellers.push(this.sellersData[1]);
	  	this.sellers.push(this.sellersData[2]);
  	},
  	tagStyle (t) {
  		let s="";
  		switch(t){
  			case "品牌" : s="brand"; break;
  			case "星选" : s="star"; break;
  			case "新店" : s="new"; break;
  			case "年货节" : s="festival"; break;
  		}
  		return s;
  	},
  	goShop (shopId) {
  		this.$router.push(`/shop/${shopId}`)
  	},
  	preferentialServiceMore (id) {
  		document.getElementById(`${id}`).style.height!=="auto" ? document.getElementById(`${id}`).style.height="auto" : document.getElementById(`${id}`).style.height="20px";
  	},
  	nearbyMore (index) {
  		this.popupIndex=index;
  		this.popupVisible = !this.popupVisible;
  	},
  	loadMore () {
  		let i=this.sellers.length;
	  	let j=this.sellersData.length;
	  	if(i<j){
	  		Indicator.open({text: '加载中...', spinnerType: 'fading-circle'});
	  		setTimeout(() => {
				this.sellers.push(this.sellersData[i]);
		    	Indicator.close();
		  	}, 1000);
	  	}else{
	  		this.$toast({message:"没有了",duration:1000});
	  		this.isBottom=true;
	  	}
  	},
  	getLocationPosition(){
  		var _this=this;
  		var geolocation = new BMap.Geolocation();
		geolocation.getCurrentPosition(function(r){
			if(this.getStatus() == BMAP_STATUS_SUCCESS){
				var pt=r.point; 
				if(pt.lng && pt.lat){
					var geoc = new BMap.Geocoder();    
	  				geoc.getLocation(pt, rs=>{ 
						var addComp = rs.addressComponents;
						_this.locationPosition="";
						if(addComp.city)_this.locationPosition+=addComp.city;
						if(addComp.district)_this.locationPosition+=addComp.district;
						if(addComp.street)_this.locationPosition+=addComp.street;
						if(addComp.streetNumber)_this.locationPosition+=addComp.streetNumber;
					});
				}else{
					_this.locationPosition='获取经纬度失败：'+JSON.stringify(r);
				}
			}else {
				_this.locationPosition='检索失败：'+this.getStatus();
			}        
		},{enableHighAccuracy: true})


  	} 
  }
}
</script>
<style scoped lang="less">
.mint-popup{transition:none;}
.index{
	.address{position: relative;color:#fff;display:block;padding:10px 10px 5px 33px;background-image: linear-gradient(90deg,#0af,#0085ff);box-sizing: border-box;
		.icon-zuobiaofill{position: absolute;left: 15px;top:16px;z-index: 2;}
		.icon-xiangxia1{font-size:.8em;position: absolute;right: 15px;top:14px;z-index: 2;}
		span{width:calc(100% - 20px);font-size: 1.2em;font-weight:bolder;overflow: hidden;white-space: nowrap;text-overflow: ellipsis;display:inline-block;position: relative;top:5px;}
	}
	.header{background-image: linear-gradient(90deg,#0af,#0085ff);box-sizing: border-box;padding:10px 15px;width:100%;
		.search{background-color: #fff;display: block;padding:10px 0;color:#999;text-align: center;
		}
		&.fixed{position: fixed;top:0;left: 50%;z-index:10;max-width:640px;transform: translate(-50%,0);}
	}
	.hotMenu{padding:10px 0 15px;box-sizing: border-box;margin:0 10px;
		a{text-align: center;display:block;color: #666;padding:0 0 5px 0;font-size: .9em;}
		img{display:block;margin:0 auto;width: 60%;height: auto;}
		.swiper-pagination{overflow:hidden;display:flex;justify-content: center;}
	}
	.channelList{margin:0 10px 10px;display:flex;justify-content: space-around;
		a{border-radius: 5px;border:1px solid #eee;padding:5px 8.5px;font-size: .8em;color: #666;
			img{display:inline-block;width: 25px;height: auto;vertical-align: middle;margin-right:5px;}
			&:last-child{
				img{width: 20px;border-radius: 3px;}
			}
		}
	}
	.openMember{display:block;margin:0 10px;height:40px;line-height:40px;background-image: linear-gradient(90deg,#ffefc4,#f3dda0);color: #644f1b;margin-bottom: 5px;
		.wangguan{width: 18px;height: auto;display:inline-block;position: relative;top:4px;margin-left: 15px;margin-right: 5px;}
		.bolder{font-weight: bolder;font-size: 1.1em;position: relative;top:1px;}
		.desc{font-size: .8em;}
		.tips,.icon-arrowright{font-size: 10px;float: right;position: relative;top:1px;}
		.icon-arrowright{margin:0 10px;}
	}
	.qualityPackage{display:block;margin:0 10px 5px;height:100px;background: linear-gradient(0deg,#f4f4f4 5%,#fafafa 95%);position: relative;padding-top:15px;
		img{position: absolute;top:3px;right:10px;width: 45%;display:block;height: auto;}
		span{display:block;margin-left: 5%;margin-bottom: 5px;}
		.tit{font-size:1rem;font-weight: 700;color: #333;}
		.subTit{font-size:.9em;color: #777;}
		.buy{font-size:.8em;color: #af8260;font-weight: 700;}
	}
	.adBanner{margin:0 10px;
		a{display:block;
			img{width:100%;display:block;height: auto;}
		}
	}
	.sellerTop{text-align: center;padding: 20px 0 10px;background-color: #fff;}
	.sellerHeader{display:flex;justify-content: center;padding:0 10px;box-sizing: border-box;
		button{background-color: transparent;border:none;width:25%;padding:10px 0;outline: none;
			.icon-xiangxia1{font-size: .4em;padding-left: 2px;}
		}
		&.fixed{position: fixed;left:50%;top:62px;z-index: 10; width:100%;max-width:640px;transform: translate(-50%,0);background-color: #f5f5f5;}
	}
	.sellerList{background-color: #fff;padding-bottom: 50px;
		li{padding:10px;box-sizing: border-box;
			.cover{display: block;width:100%;background-color: transparent;border:none;text-align: left;outline: none;
				&>.fl{width:25%;position: relative;border-radius: 5px;overflow:hidden;
					img{display:block;border-radius: 5px;width:100%;height: auto;border:1px solid #eee;box-sizing: border-box;}
					span{position: absolute;top:0px;left: 0;z-index: 2;display:inline-block;font-size:.8em;padding:0 3px;border-radius:0 0 5px 0;height: 14px;line-height: 15px;}
					.festival{background-color: #FC6305;color: #fff;}
					.brand{background-color: #ffe339;color: #6f3f15;}
					.star{background-color: #E71A2A;color: #fff;}
					.new{background-color:#70bc46;color: #fff;}
				}
				&>.fr{width:75%;padding-left: 10px;box-sizing: border-box;font-size:.8em;position: relative;
					.sellerName{margin-bottom: 5px;
						span{font-size: 1.3em;overflow: hidden;white-space: nowrap;text-overflow: ellipsis;max-width:90%;}
						i{color:#ddd;font-size:.4em;position: relative;top:5px;}
					}
					.score{color:#FC6305;font-size:1em;
						i{width:10px;font-size:1.1em;display:inline-block;margin-right:5px;}
					}
					.sales{color: #777;padding-left:5%;
						&:before{content:"月售";}
					}
					.hummingbird{height:16px;line-height:16px;padding:0 3px;background-color: #09A4F3;color:#fff;}
					.onTime{height:16px;line-height:16px;padding:0 3px;color: #09A4F3;border:1px solid #09A4F3;box-sizing: border-box;}
					.fontGray{color:#777;height: 20px;line-height: 20px;
						.fl:first-child,.fr:last-child{margin-right:5%;}
					}
					.evaluate{background-color: #FEF6EE;display:inline-block;width: auto;padding:0 5px;color:#F59140;margin-bottom: 5px;}
					.preferentialService{margin-bottom: 5px;padding-right:40px;position:relative;height:20px;overflow:hidden;transition: all 0.3s;
						&.showMore{height: auto;}     
						.service{margin-left: -3px;}
						i{font-style: normal;color:#777;height:18px;line-height:18px;border:1px solid #ddd;box-sizing: border-box;padding:0 2px;margin-right:3px;display:inline-block;margin-bottom: 3px;white-space: nowrap;}
						.youhui{i{color:#E6625C;border-color:#fdbfbd;}}
						.huiyuan{i{color:#9c573b;border-color:#9c573b;}}

					}
					.nearby{color:#777;padding-top: 2px;border-top: 1px solid #f7f7f7;position: relative;clear: left;}
					.preferentialServiceMore{position: absolute;top:0px;right:-5px;z-index:2;background-color: transparent;border:none;outline: none;
						i{font-size: 10px;border:none;color:#ddd;}}
					.nearbyMore{position: absolute;top:5px;right:0px;z-index:2;background-color: transparent;border:none;outline: none;
						i{font-size: 10px;border:none;color:#ddd;}}
				}
			}
		}
	}
	.demoList{width:100%;height: 50px;border-bottom: 1px solid #ddd;background-color: #ccc;line-height: 50px;text-align: center;}
	.mint-popup {width: 90%;padding:10px 0;border-radius: 5px;
		li{line-height: 35px;color: #666;
			span:first-child{max-width:60%;display:inline-block;overflow: hidden;white-space: nowrap;text-overflow: ellipsis;}
		}
	}

}
@media only screen and (max-width:320px) {
	.index .channelList a{padding-left: 2.5%;padding-right:2.5%;}
}
@media only screen and (min-width:321px) {
	.index .channelList a{padding-left: 3.7%;padding-right:3.7%;}
	.index .mint-popup li span:first-child{max-width:65%;}
}
@media only screen and (min-width:414px) {
	.index .channelList a{padding-left: 4.8%;padding-right:4.8%;}
	.index .mint-popup li span:first-child{max-width:70%;}
}
</style>



