<template>
<div class="mainFooter">
	<router-link v-for="(l,index) in list" :key="index" :to="l.url" :class="l.hover">
		<i class="iconfont" :class="l.icon"></i>
		<span>{{l.name}}</span>
	</router-link>
</div>
</template>

<script>
export default {
	name: 'mainFooter',
	data () {
		return {
			msg: 'mainFooter',
			list:[
				{
					hover:"on",
					icon:"icon-changyonglogo40",
					name:"外卖",
					url:"/"
				},{
					hover:"",
					icon:"icon-faxian2",
					name:"发现",
					url:"/find/index"
				},{
					hover:"",
					icon:"icon-dingdan",
					name:"订单",
					url:"/order/index"
				},{
					hover:"",
					icon:"icon-account",
					name:"我的",
					url:"/profile/index"
				}
			]
		}
	}
}
</script>

<style scoped lang="less">
@base:#29B1FF;
.mainFooter{position: fixed;left:50%;bottom: 0;z-index: 2; width:100%;max-width:640px;height: 50px;border-top: 1px solid #ddd;box-sizing: border-box;background-color: #fff;display:flex;justify-content: center;transform: translate(-50%, 0);
	a{
		flex: 1;
		i{display: block;font-style: normal;text-align: center;margin-bottom: 5px;color:#666;
			&:before{font-size: 1.2em;position: relative;top:7px;}
		}
		span{display: block;text-align: center;color:#666;}
		&.router-link-exact-active{
			i{color:@base;}
			span{color:@base;}
		}
		&:first-child{
			i{margin-bottom:0px; &:first-child:before{font-size: 2.2em;top:8px;}}
		}
	}
}

</style>


