<template>
<div class="shopInfo">
	<ul class="header">
		<li>
			<em>起送价</em>
			<p>
				<span>{{shop.shop.startUpSend}}</span>
				<i>元</i>
			</p>
		</li>
		<li>
			<em>商家配送</em>
			<p>
				<span>{{shop.shop.freight}}</span>
				<i>元</i>
			</p>
		</li>
		<li>
			<em>平均配送时间</em>
			<p>
				<span>{{shop.shop.duration}}</span>
				<i>分钟</i>
			</p>
		</li>
	</ul>
	<dl class="notice part">
		<dt>活动公告</dt>
		<dd>
			<p>粥品香坊其烹饪粥料的秘方源于中国千年古法，在融和现代制作工艺，由世界烹饪大师屈浩先生领衔研发。坚守纯天然、0添加的良心品质深得消费者青睐，发展至今成为粥类的引领品牌。是2008年奥运会和2013年园博会指定餐饮服务商。</p>
			<ul>
				<li v-for="(o,index) in shop.shop.offer" v-if="o.list && o.list.length>0" :key="index">
					<i :class="filterServiceStyle(o.name)">{{o.name}}</i>
					<div>
						<span v-for="(s,index) in o.list" :key="index">{{s.name}}</span>
					</div>
				</li>
			</ul>
		</dd>
	</dl>
	<dl class="photos part">
		<dt>商家实景</dt>
		<dd class="w oh mb10"> 
			<img v-for="(p,index) in buildArray(photos)" :key="index" :src="p" alt="" v-preview="p" preview-title-enable="true" preview-nav-enable="true" />
		</dd>
		<dd>{{shop.shop.announcement}}</dd>
	</dl>
	<dl class="info part">
		<dt>商家信息</dt>
		<dd>
			<div class="w oh">
				<span class="fl">商家名称</span>
				<span class="fr">{{shop.shop.name}}</span>
			</div>
			<div class="w oh">
				<span class="fl">商家品类</span>
				<span class="fr">
					<i v-for="(c,index) in shop.shop.category" :key="index">{{c}}</i>
				</span>
			</div>
			<div class="w oh">
				<span class="fl">商家地址</span>
				<span class="fr">{{shop.shop.address.province}} {{shop.shop.address.city}} {{shop.shop.address.country}} {{shop.shop.address.area}}</span>
			</div>
			<div class="w oh">
				<span class="fl">商家电话</span>
				<span class="fr"><a :href="'tel:'+shop.shop.tel">{{shop.shop.tel}}</a></span>
			</div>
			<div class="w oh">
				<span class="fl">营业时间</span>
				<span class="fr">{{shop.shop.start_business_time}} - {{shop.shop.end_business_time}}</span>
			</div>
		</dd>
	</dl>
	<dl class="service part">
		<dt>商家服务</dt>
		<dd>
			<p v-for="(s,index) in shop.shop.service" :key="index"><i>{{s.name.substr(0,1)}}</i><span>{{s.desc}}</span></p>
		</dd>
	</dl>
</div>
</template>

<script>
import imagePreview from 'vue-image-preview';
export default {
	name: 'shopInfo',
	data () {
		return {
			shopId: this.$route.params.shopId,
			shop:{},
			photos:['b6c75bd250e5ba69868f3b1178afbda3jpeg','f963d608c5811bc2d902fc9ab9a5baa7jpeg']
		}
	},
	watch: {
	
	},
	created(){
		if(localStorage[this.shopId]){
			this.shop=JSON.parse(localStorage[this.shopId])
		}
	},
	computed: {
	
	},
	methods: {
		buildArray(list){
			let arr=[];
			list.forEach(item=>{
				arr.push(this.imageSrcBuild(item));
			})
			return arr;
		},
		imageSrcBuild(str){
			let a=str.substr(0,1);
			let b=str.substr(1,2);
			let c=str.substr(3);
			return `http://fuss10.elemecdn.com/${a}/${b}/${c}.jpeg`;
		},
		filterServiceStyle(s){
			let a='';
			switch(s){
				case '满减' : a='jian'; break;
				case '折扣' : a='piao'; break;
				case '首单' : a='zhe'; break;
				case '特价' : a='te'; break;
				case '满赠' : a='piao'; break;
				case '新客' : a='bao'; break;
				case '会员' : a='piao'; break;
				case '红包' : a='jian'; break;
			}
			return a;
		}
	}
}
</script>

<style scoped lang="less">
.shopInfo{padding-bottom:100px;
	.header{display: flex;justify-content: center;padding:30px 0;border-top: 1px solid #eee;
		li{width: 33.33%;text-align: center;
			&:nth-child(2){border-left: 1px solid #eee;border-right: 1px solid #eee;}
			em{color:#999;}
			p{padding:10px 0;
				span{font-size: 24px;color: #333;}
				i{font-style: normal;color:#999;}
			}
		}
	}
	.part{padding:15px 5%;box-sizing: border-box;border-top: 10px solid #eee;
		dt{font-size: 14px;color:#333;line-height: 40px;}
		dd{font-size: 12px;color: #666;line-height: 25px;}
	}
	.photos dd img{width:23%;float:left;border-radius: 2px;height: auto;display: block;margin-right:2.666%;}
	.photos dd img:nth-child(4n){margin-right: 0;}
	.notice p{color:#f90;margin-bottom: 10px;}
	.notice dd i{font-style: normal;width:auto;padding:0 2px;height:18px;line-height:18px;text-align:center;font-family:SimSun;font-size: 12px;border-radius: 2px;display:inline-block;border-width: 1px;border-style: solid;box-sizing:border-box;}
	.notice dd div{display:inline-block;padding-left: 5px;font-size: 12px;}
	
	.jian{color: #F02121;border-color:#F02121;}
	.zhe{color: #008BC7;border-color:#008BC7;}
	.te{color: #07B641;border-color:#07B641;}
	.piao{color: #9D2290;border-color:#C478BC;}
	.bao{color:#0BCE9D;border-color:#0BCE9D;}
	.notice dd ul li{height: 30px;line-height: 30px;}
	.info{
		i{font-style: normal;
			&:after{content:" / ";}
			&:last-child:after{content:"";}
		}
		.w{margin-bottom: 10px;}
		.w .fr{width:60%;text-align:right;line-height: 20px;color:#999;
			a{color:#2395ff;}
		}
	}
	.service{
		p{width: 100%;overflow:hidden;margin-bottom: 20px;}
		i{font-style: normal;margin-right: 10px;display:inline-block;float: left;overflow:hidden;border:1px solid #ddd;box-sizing:border-box;line-height: 20px;width:20px;height: 20px;text-align: center;border-radius: 2px;}
		span{display:inline-block;width:calc(100% - 60px);float: left;line-height: 20px;}
	}
}
</style>


