<template>
<div class="food">
	<div class="product">
		<div class="header">
			<button class="back" @click='back'><i class="iconfont icon-back"></i></button>
		</div>
		<img class='picture' :src="product.icon" alt="" v-preview="product.icon" preview-title-enable="true" preview-nav-enable="true" />
		<p class="name">{{product.name}}</p>
		<p class="desc">{{product.desc}}</p>
		<div class="info">
			<span class="price">{{product.price}}</span>
			<div class="set">
				<button @click='update(2)' class="jionCart" v-if='product.buyCount==0'>加入购物车</button>
				<div v-else>
					<button class="less" @click='update(1)'><i class="iconfont icon-shanchu6"></i></button>
					<span>{{product.buyCount}}</span>
					<button class="add" @click='update(2)'><i class="iconfont icon-jiahao2fill"></i></button>
				</div>
			</div>
		</div>	
	</div>
	<div class="comment" v-if='comments.length>0'>
		<div class="header">
			<div class="title">
				<span class="left">商品评价</span>
				<span class="right">共{{comments.length}}条</span>
			</div>	
			<div class="tab">
				<button :class="tab[0]" @click='switchTab(0)'>全部 {{comments.length}}</button>
				<button :class="tab[1]" @click='switchTab(1)'>有图 {{hovePictures}}</button>
				<button :class="tab[2]" @click='switchTab(2)'>好评 {{hoveGood}}</button>
				<button :class="tab[3]" @click='switchTab(3)'>吐槽 {{haveBad}}</button>
			</div>
		</div>
		<ul class="list">
			<li v-for='(item,index) in commentsList' :key='index'>
				<div class="left"><img :src="item.userIcon" alt="" /></div>
				<div class="right">
					<div class="top">
						<p class="userName">{{item.userName}}</p>
						<time class="createTime">{{item.createTime}}</time>
					</div>
					<div class="contents">
						<span class="prodName">{{product.name}}</span>
						<span class="userSay">{{item.content}}</span>
					</div>
					<div class="footer" v-if='item.image'>
						<img v-for='(img,index) in item.image' :key='index' :src="img" alt="" v-preview="item.image" preview-title-enable="true" preview-nav-enable="true" />
					</div>
				</div>
			</li>
		</ul>
	</div>
	<div class="noComment" v-else>
		<img src="https://shadow.elemecdn.com/faas/desktop/media/img/profile-loading.4984fa.gif" alt="">
		<p>还没有人评价过...</p>
	</div>
	<shopBottomCart :shopID='shopId'></shopBottomCart>
	<managerBottomCart :shopID='shopId'></managerBottomCart>
</div>
</template>

<script>
import shopBottomCart from './cart.vue';
import managerBottomCart from './managerBottomCart.vue';	
import imagePreview from 'vue-image-preview';
export default {
	name: 'food',
	components: {shopBottomCart, managerBottomCart},
	data () {
		return {
			prodId:this.$route.params.foodID,
			shopId:'',
			product:{},
			seller:{},
			tab:[],
			commentsList:[],
			comments:[
				{
					userName:'W**********⊙',
					userIcon:'https://fuss10.elemecdn.com/1/3f/ac4ce1fa61a49a7d1f8c93b2f5f02jpeg.jpeg',
					createTime:'2019-03-05 12:23:30',
					content:'宫保鸡丁没黄瓜、没胡萝卜，挺会偷工减料的。大拌菜不给酱',
					image:['https://fuss10.elemecdn.com/a/dd/0118fbf759eb8f870bd3922f7157cjpeg.jpeg'],
					good:false,
					bad:true
				},{
					userName:'匿**户',
					userIcon:'https://fuss10.elemecdn.com/a/47/995284be2d930bc2bb325d8ca90bbjpeg.jpeg',
					createTime:'2019-03-06 19:45:10',
					content:'菜里有不干净杂质！冰红茶不放柠檬！',
					image:[],
					good:false,
					bad:true
				},{
					userName:'V********9',
					userIcon:'https://fuss10.elemecdn.com/e/41/9a593743907c6d4f66098630144bfjpeg.jpeg',
					createTime:'2019-03-07 13:21:49',
					content:'外卖给的量实在太少了，比起店里堂食感觉少很多',
					image:[],
					good:false,
					bad:true
				},{
					userName:'3*******4',
					userIcon:'https://fuss10.elemecdn.com/1/86/cc055f696ec6a52782e9c47e730e5jpeg.jpeg',
					createTime:'2019-03-07 14:36:58',
					content:'一如既往的好',
					image:[],
					good:true,
					bad:false
				}
			]
		}
	},
	computed:{
		hovePictures(){
			let list=this.comments;
			let num=0;
			if(list.length>0){
				list.forEach(item=>{
					item.image.length>0 ? num++ : ''
				})
			}else{
				num=0;
			}
			return num;
		},
		hoveGood(){
			let list=this.comments;
			let num=0;
			if(list.length>0){
				list.forEach(item=>{
					item.good ? num++ : ''
				})
			}else{
				num=0;
			}
			return num;
		},
		haveBad(){
			let list=this.comments;
			let num=0;
			if(list.length>0){
				list.forEach(item=>{
					item.bad ? num++ : ''
				})
			}else{
				num=0;
			}
			return num;
		}
	},
	created(){
		window.scroll(0,0);
		this.init();
	},
	mounted(){
		this.BUS.$on('flush', ()=>{
			this.init();
		})
		this.BUS.$on('clearCart', ()=>{
			this.init();
		})
	},
	methods: {
		init(){
			if(localStorage.shopId){
				this.shopId=localStorage.shopId;
				if(localStorage[this.shopId]){
					this.seller=JSON.parse(localStorage[this.shopId]);
					let goods=this.seller.goods;
					goods.forEach( item => {
						if(item.id===this.prodId) this.product=item;
					})
				}
			}
			this.tab[0]='on';
			this.commentsList=this.comments;
		},
		back(){
			this.$router.go(-1);
		},
		update(t){
			this.BUS.$emit('getProdID',t,this.prodId);
			if(t==1 && this.product.buyCount>0) {
				this.product.buyCount--;
			}
			this.init();
		},
		switchTab(i){
			this.tab=[];
			this.tab[i]='on';
			let list=this.comments;
			let arr=[];
			switch(i){
				case 0: arr = list; break;
				case 1: list.forEach( item => {item.image.length>0 ? arr.push(item) : ''}); break;
				case 2: list.forEach( item => {item.good ? arr.push(item) : ''}); break;
				case 3: list.forEach( item => {item.bad ? arr.push(item) : ''}); break;
			}
			this.commentsList = arr;
		}
	}
}
</script>

<style scoped lang="less">
.food{background-color: #fff;padding-bottom: 100px;
	.product{position: relative;
		.header{position: absolute;width:100%;height: 40px;
			.back{width:40px;height: 40px;text-align: center;line-height: 40px;
				i{font-size: 1.2em;color:#2396ff;}
			}
		}
		.picture{width:100%;height: auto;display:block;}
		.name{font-size: 16px;padding:10px 5%;box-sizing:border-box;}
		.desc{font-size: 14px;padding:0px 5% 10px;color:#999;box-sizing:border-box;}
		.info{padding: 0 5% 20px;box-sizing:border-box;overflow: hidden;
			.price{color:#ff5339;font-size: 16px;float: left;
				&:before{content:"￥";font-size: 12px;}
			}
			.set{float: right;
				.jionCart, button i{color:#2396ff;}
				span{color:#666;position: relative;top:-1px;}
			}
		}
	}
	.comment{border-top: 10px solid #eee;
		.header{width:100%;height: auto;padding:10px 5% 0;box-sizing:border-box;
			.left{float: left;}
			.right{float: right;}
			.title{width: 100%;overflow: hidden;
				.left{color:#333;}
				.right{color:#999;}
			}
			.tab{width: 100%;overflow: hidden;display:flex;justify-content: start;padding-top: 20px;align-items: center;
				button{width:15%;height: 30px;line-height: 30px; border-radius:3px;margin-right: 3%;color:#666;padding:0;transition: all 0.3s;background-color: #dfebf7;font-size: 14px;
					&:last-child{margin-right:0;}
					&.on{background-color: #2396ff;color:#fff;}
				}
			}
		}
		.list{padding: 10px 0 0;
			li{padding:10px 5%;width:100%;overflow: hidden;box-sizing:border-box;border-bottom: 1px dashed #eee;
				.left{float: left;width:40px;height: 40px;overflow: hidden;border-radius: 100%;
					img{width: 100%;height: auto;display:block;}
				}
				.right{float: right;padding-left: 10px;width:calc(100% - 40px);box-sizing:border-box;
					.top{width: 100%;overflow: hidden;line-height: 30px;
						.userName{float: left;color:#666;font-size: 14px;}
						.createTime{float: right;color:#999;font-size: 12px;}
					}
					.contents{font-size: 14px;
						.prodName{color:#999;margin-left: -7px;
							&:before{content:"「";}
							&:after{content:"」";}
						}
						.userSay{color:#000;}
					}
				}
			}
		}
		.footer{width:100%;height: auto;padding:10px 0 0 0;box-sizing: border-box;overflow: hidden;
			img{width:23%;height: auto;display:block;float:left;margin-right: 2.333%;margin-bottom:2.333%;border-radius: 5px;}
			img:nth-child(4n){margin-right: 0;}
		}
	}
	.noComment{border-top: 10px solid #eee;padding:50px 0 0;
		img{width:20%;display:block;height: auto;margin:0 auto;}
		p{text-align: center;font-size: 14px;color:#999;line-height: 50px;}
	}
}
</style>


