<template>
	<div class="shopBottomCart" :class="empty">
		<div class="icon" ref='cart'>
			<i class="iconfont icon-gouwuche"></i>
			<span v-if="empty!=='empty'">{{cart.buyCount}}</span>
		</div>
		<div class="total">
			<p class="payPrice">{{payPrice}}</p>
			<p class="freight" v-if="seller && seller.shop && seller.shop.freight>0">另需配送费{{seller.shop.freight}}元</p>
		</div>
		<button class="buyBtn" @click="goPay">{{buyBtnText}}</button>
		<button class="showCartList" @click='showCartList'></button>
	</div>
</template>

<script>
import { MessageBox } from 'mint-ui';	
export default {
	name: 'shopBottomCart',
	props:['shopID'],
	data () {
		return {
			shopId: this.$route.params.shopId||this.shopID,
			seller:{},
			payPrice:"", //统计当前店铺的购物金额
			empty:"",//   notEnough
			buyBtnText:"", // 还差￥17起送    去结算
			cart:{//购物车商品列表
				buyCount:0, //统计当前店铺的购物数量
				total:0, //统计当前店铺的购物金额
				list:[]
			},
			startUpSend:'' 
		}
	},
	created(){
		this.init();
	},
	watch:{
		payPrice(){
			let startUpSend=this.startUpSend;
			if(this.payPrice == 0 || this.payPrice == '未选购商品'){
				this.buyBtnText=`￥${startUpSend}起送`;
				this.payPrice = '未选购商品';
				this.empty='empty';
			}else if(this.payPrice < startUpSend){
				this.buyBtnText=`还差￥${startUpSend - this.payPrice}起送`;
				this.empty='notEnough';
			}else if(this.payPrice > startUpSend){
				this.buyBtnText='去结算';
				this.empty='';
			}
		}
	},
	mounted () {
		let oThis = this;
		//t=1是减   t=2是加  
		this.BUS.$on('getProdID', (t,prodId,event,shopId)=>{
			//if(event)this.fly(t,event);
			let cartList = oThis.cart.list;
			let goods = oThis.seller.goods;
			//如果是从购物车中减商品
			if(t==1){
				//改变商品列表内容
				goods.forEach( g => {
					if(prodId===g.id){
						g.buyCount--;
					}
				})
				//改变购物车列表内容
				let arr=[];
				cartList.forEach( l => {
					if(l.id===prodId){
						l.buyCount--;
						oThis.cart.buyCount --;
						oThis.cart.total -= l.price;
					}
					if(l.buyCount>0){
						arr.push(l);
					}
				})
				cartList=arr;
			//如果是加入购物车
			}else if(t==2){
				//如果购物车不为空，则遍历增加
				if(cartList && cartList.length>0){ 
					let flag = cartList.some(i=>i.id===prodId);
					if(flag){
						cartList.forEach(l => {
							if(l.id==prodId){
								l.buyCount++;
								oThis.cart.total += l.price;
								oThis.cart.buyCount ++;
							}
						});	
						goods.forEach(g=>{
							if(g.id === prodId){
								g.buyCount ++;
							}
						})
					//如果当前点击的商品，不在购物车，则新添加	
					}else{
						goods.forEach(g=>{
							if(g.id === prodId){
								g.buyCount ++;
								cartList[cartList.length]=JSON.parse(JSON.stringify(g));
								oThis.cart.buyCount ++;
								oThis.cart.total += g.price;
							}
						})
					}
				//若购物车为空，则直接追加一个商品
				}else{
					goods.forEach(g=>{
						if(g.id === prodId){
							g.buyCount ++;
							cartList[0]=JSON.parse(JSON.stringify(g));
							oThis.cart.buyCount ++;
							oThis.cart.total = g.price;
						}
					})
				}
			}
			oThis.payPrice = oThis.cart.total;
			//回归购物车信息
			oThis.cart.list = cartList; 
			oThis.seller.cart = oThis.cart;
			oThis.seller.goods = goods;
			//缓存信息
			localStorage[oThis.shopId]=JSON.stringify(oThis.seller);
			this.BUS.$emit('flush');
			if(cartList.length==0){
				this.BUS.$emit('clear');
			}
		})
		this.BUS.$on('clearCart', ()=>{
			this.init();
		})
	},
	methods: {
		init(){
			if(localStorage[this.shopId]){
				this.seller = JSON.parse(localStorage[this.shopId]);
				this.startUpSend = this.seller.shop.startUpSend;
				this.buyBtnText = `￥${this.startUpSend}起送`;
				//判断购物车信息
				if(this.seller.cart && this.seller.cart.buyCount>0){
					this.cart=this.seller.cart;
					this.payPrice=this.cart.total;
				}else{
					this.payPrice=0;
					this.cart={buyCount:0,total:0,list:[]};
				}
			}
		},
		//点击去结算按钮事件
		goPay(){
			if(this.buyBtnText==="去结算"){
				MessageBox('支付', '您需要支付'+this.cart.total+'元');
			}
		},
		showCartList(){
			if(this.cart.buyCount>0){
				this.BUS.$emit('showCartList');
			}else{
				this.$toast({message: '购物车还是空的', duration: 1200});
			}
		},
		fly(t,e){
			if(t==2 && e){
				if(!document.getElementById('fly')){ 
					let x=e.x;
					let y=e.y;
					//创建div
					let div=document.createElement('div');
					div.id='fly';
					div.innerHTML=1;
					div.style.top=y-8+'px';
					div.style.left=x-8+'px';
					document.body.appendChild(div);

					//let w=document.body.offsetWidth;
					let h=document.body.offsetHeight;
					let flyWidth=x-40;
					let flyHeight=h-30;
					console.log(`x=${x}, y=${y}`);
					console.log(`flyWidth=${flyWidth}, flyHeight=${flyHeight}`);
					//开始动画
					setTimeout(()=>{ 
						let fly=document.querySelector("#fly");
						if(fly.length>0){
							fly.style.transform = "translate(-"+flyWidth+"px, "+flyHeight+"px)";
						}
					},100)
					
					//消除div
					// setTimeout(()=>{
					// 	var ele=document.getElementById('fly'); 
					// 	ele.parentNode.removeChild(ele);
					// },2000)
				}
			}
		}
	}
}
</script>

<style scoped lang="less">
.shopBottomCart{position:fixed;left: 0;bottom: 0;z-index: 10;width: 100%;height: 50px;display:flex;background-image:linear-gradient(left, #f86627, #e6291b);
	&.empty{color:#999;background-color:rgba(0,0,0,0.8);background-image:none;
		.icon-gouwuche{color:#666;}
		.total,.payPrice{color:#999;}
		.payPrice{font-size: 1.1em;font-weight: normal;}
		.payPrice:before{content:"";}
		.buyBtn{background-color: #535356;color: #a9a9ab;}
	}
	&.notEnough{color:#fff;
		.icon-gouwuche{color:#fff;}
		.buyBtn{background-color: #ffbb39;color: #ffdd9c;}
	}
	.total{position: relative;left: 25%;padding-top: 5px;color:#fff;}
	.payPrice{margin-bottom: -3px;font-size: 1.5em;color:#fff;font-weight: bolder;}
	.payPrice:before{content:"￥";font-size: 10px;font-weight: normal;}
	.freight{font-size: 10px;}
	.icon{position: absolute;top:0;left: 5%;}
	.icon span{position: absolute;right: -10px;top:5px;z-index: 2;width:20px;height: 20px;text-align: center; background-color:#ffbb39;color:#fff;border-radius: 100%;display:block;font-size: 10px;background-image: -webkit-gradient(linear, left top, right top, from(#e6291b), to(#ffbb39));}
	.icon-gouwuche{font-size: 38px;position: relative;top:18px;color:#fff;}
	.buyBtn{position: absolute;top:0;right: 0;z-index: 1;width: 30%;height: 100%;background-color: #ffbb39;font-size:1.1em;color:#fff;font-weight:bolder;}
	.showCartList{position: absolute;left: 0;z-index: 10;top:0;width:70%;height: 50px;}
}
</style>


